.estimated {
  position: relative;
  margin-bottom: 20px;
  margin-top: 30px;
  display: flex;
  align-items: center;
  padding: 9px 30px;
  background: #f7edff;
  border-radius: 10px;
  @include response(767px) {
    padding-left: 20px;
    padding-right: 20px;
    justify-content: center;
  }
  &__icon {
    flex: 0 0 42px;
    max-width: 42px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    width: 42px;
    height: 42px;
    margin-right: 15px;
  }
  &__text {
    font-size: 14px;
    line-height: 180%;
    color: #333;
  }
}
