.custom-table {
  position: relative;
  margin-bottom: 25px;
  color: #333;
  display: flex;
  @include response(767px) {
    display: block;
  }
  &__head {
    margin-bottom: 21px;
    .custom-table__cell {
      font-size: 20px;
      line-height: 160%;
      font-weight: 500;
      padding-right: 30px;
      @include response(767px) {
        padding-right: 0;
      }
      br {
        display: none;
        @include response(1200px) {
          display: inline;
        }
        @include response(767px) {
          display: none;
        }
      }
    }
  }
  &__holder {
    width: 50%;
    @include response(767px) {
      width: 100%;
    }
  }
  &__cell {
    position: relative;
    font-size: 16px;
  }
  &__body {
    position: relative;
    .custom-table__cell {
      padding: 13px 60px;
      border-radius: 10px;
      @include response(767px) {
        padding-right: 20px;
      }
    }
  }
  &__row {
    position: relative;
    display: flex;
    &:nth-child(even) {
      background: rgba(#fef5f6, 0.5);
    }
  }
  &__cell-decor {
    position: absolute;
    width: 10px;
    height: 10px;
    background: #6c41bf;
    border-radius: 50%;
    top: 50%;
    margin-top: -5px;
    left: 30px;
  }
}
