.content {
  position: relative;
  padding-top: 75px;
  padding-bottom: 100px;
  @include response(1200px) {
    padding-top: 50px;
    padding-bottom: 50px;
    display: none;
  }
  &--new {
    @include response(1200px) {
      display: block;
    }
  }
  &__box {
    position: relative;
    &--new {
      max-width: 825px;
      width: 100%;
      margin: 0 auto;
    }
  }
}