.custom-list {
  position: relative;
  display: flex;
  @include response(767px) {
    flex-wrap: wrap;
  }
  &__item {
    flex: 0 0 20%;
    max-width: 20%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include response(767px) {
      flex: 0 0 50%;
      max-width: 50%;
      margin-bottom: 30px;
      &--last {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 0;
      }
    }
  }
  &__icon {
    height: 62px;
    flex: 0 0 auto;
    @include response(767px) {
      .custom-list__item--last & {
        display: none;
      }
    }
  }
  &__item-text {
    width: 100%;
    padding: 16px;
    font-size: 14px;
    line-height: 160%;
    text-align: center;
    text-transform: uppercase;
    color: #333;
    border: 1px solid #e9e9e9;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    .custom-list__item--last & {
      background: #f8f8f8;
      color: #a6b6b6;
    }
  }
}
