.about {
  position: relative;
  padding-top: 147px;
  @include response(1200px) {
    padding-top: 40px;
  }
  &__top {
    position: relative;
    text-align: center;
    margin-bottom: 80px;
    @include response(1200px) {
      margin-bottom: 40px;
    }
  }
  &__content-small {
    max-width: 782px;
    width: 100%;
    margin: 0 auto;
  }
  &__title {
    font-size: 50px;
    line-height: 80px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 10px;
    @include response(1200px) {
      font-size: 30px;
      line-height: 40px;
      text-align: left;
    }
  }
  &__descriptions {
    font-size: 18px;
    line-height: 28px;
    text-align: justify;
    text-align-last: center;
    @include response(767px) {
      text-align: left;
      text-align-last: left;
    }
    p {
      margin: 0;
    }
  }
  &__decor {
    display: flex;
    align-items: center;
    margin-left: -129px;
    margin-right: -129px;
    margin-bottom: -30px;
    position: relative;
    @include response(1200px) {
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 0;
      padding-top: 30px;
      padding-bottom: 30px;
    }
    @include response(767px) {
      flex-direction: column;
    }
    &::before {
      content: "";
      position: absolute;
      top: 95px;
      left: 129px;
      width: 390px;
      background: $purple;
      height: 1px;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__decor-img {
    flex: 0 0 auto;
    margin-left: 66px;
    @include response(1200px) {
      flex: 0 0 35%;
      margin-left: 20px;
    }
    @include response(767px) {
      margin-left: 0;
      margin-top: 20px;
    }
  }
  &__decor-text {
    flex: 1 1 auto;
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0;
    }
  }
  &__text-content {
    text-align: justify;
    text-align-last: center;
    @include response(767px) {
      text-align: left;
      text-align-last: left;
    }
  }
  &__content {
    max-width: 980px;
    width: 100%;
    margin: 0 auto;
  }
  &__products {
    position: relative;
    display: flex;
    padding-top: 10px;
    @include response(1200px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__products-img {
    flex: 0 0 491px;
    max-width: 491px;
    width: 100%;
    position: relative;
    padding-top: 104px;
    margin-right: 79px;
    @include response(1200px) {
      flex: 0 0 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 20px;
      img {
        width: 100%;
      }
    }
  }
  &__products-img-decor {
    position: absolute;
    top: 0;
    right: 0;
  }
  &__products-imgs {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include response(767px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__products-imgs-cell {
    flex: 0 0 225px;
    max-width: 225px;
    width: 100%;
    &:nth-child(2) {
      margin-top: 110px;
      @include response(767px) {
        margin-top: 40px;
      }
    }
  }
  &__products-img-slider {
    position: relative;
    .slick-dots {
      position: absolute;
      padding: 0;
      left: 0;
      bottom: -28px;
    }
    .slick-dots li.slick-active button {
      width: 10px;
    }
  }
  &__products-imgs-title {
    font-size: 12px;
    text-transform: uppercase;
    margin-bottom: 17px;
    &--right {
      margin-top: 17px;
      margin-bottom: 0;
      text-align: right;
    }
  }
  &__products-img-slider-item {
    border-radius: 10px;
    overflow: hidden;
    vertical-align: top;
    img {
      width: 100%;
    }
  }
  &__products-content {
    flex: 1 1 auto;
    position: relative;
    padding-top: 15px;
  }
  &__products-title {
    margin-bottom: 60px;
    font-size: 50px;
    font-weight: 700;
    @include response(1200px) {
      font-size: 28px;
      margin-bottom: 20px;
      br {
        display: none;
      }
    }
  }
  &__products-text {
    margin-bottom: 40px;
    text-align: justify;
    @include response(1200px) {
      margin-bottom: 20px;
      text-align: left;
    }
  }
  &__btn {
    text-decoration: none;
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    line-height: 24px;
    font-weight: 700;
    color: #000;
    text-transform: uppercase;
    border-bottom: 1px solid $purple;
    @include desktop-hover() {
      color: $purple;
    }
  }
  &__educate {
    position: relative;
    display: flex;
    align-items: center;
    @include response(1200px) {
      flex-direction: row-reverse;
    }
    @include response(767px) {
      flex-direction: column-reverse;
    }
  }
  &__educate-content {
    text-align: justify;
    text-align-last: right;
    margin-right: -50px;
    position: relative;
    @include response(1200px) {
      text-align: left;
      text-align-last: left;
      margin-right: 0;
    }
    @include response(767px) {
      margin-right: 0;
    }
  }
  &__educate-title {
    margin-bottom: 36px;
    font-size: 50px;
    font-weight: 700;
    @include response(1200px) {
      font-size: 28px;
      margin-bottom: 20px;
      br {
        display: none;
      }
    }
  }
  &__educate-text {
    margin-bottom: 30px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__educate-img {
    flex: 0 0 auto;
    @include response(1200px) {
      flex: 0 0 35%;
      margin-right: 20px;
    }
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 20px;
      img {
        width: 100%;
      }
    }
  }
  &__history {
    position: relative;
    padding-bottom: 100px;
    @include response(1200px) {
      padding-bottom: 50px;
    }
  }
  &__history-list {
    position: relative;
    padding-top: 30px;
    @include response(1200px) {
      padding-top: 15px;
    }
  }
  &__item {
    position: relative;
    display: flex;
    margin-bottom: 60px;
    @include response(1200px) {
      display: block;
    }
    &::before {
      content: "";
      position: absolute;
      top: 80px;
      left: 18px;
      width: 1px;
      bottom: -38px;
      background: #000;
      @include response(1200px) {
        display: none;
      }
    }
    &:last-child {
      margin-bottom: 0;
      &::before {
        display: none;
      }
    }
  }
  &__item-year {
    flex: 0 0 202px;
    max-width: 202px;
    width: 100%;
    color: #000;
    font-size: 40px;
    text-transform: uppercase;
    font-weight: 700;
    @include response(1200px) {
      margin-bottom: 10px;
      max-width: 100%;
      br {
        display: none;
      }
    }
    span {
      -webkit-text-fill-color: white;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color: black;
    }
  }
  &__item-box {
    flex: 1 1 auto;
    position: relative;
  }
  &__item-row {
    display: flex;
    position: relative;
    margin-bottom: 70px;
    @include response(1200px) {
      display: block;
      margin-bottom: 20px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-month {
    flex: 0 0 185px;
    color: #262626;
    font-size: 16px;
    line-height: 28px;
    font-weight: 700;
    text-transform: uppercase;
    padding-right: 10px;
  }
  &__item-text {
    &.text-content {
      font-size: 18px;
    }
  }
  &__questions {
    position: relative;
    background: #f2ecf9;
    padding-top: 95px;
    padding-bottom: 102px;
    text-align: center;
    @include response(1200px) {
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }
  &__questions-title {
    font-weight: 700;
    font-size: 20px;
    margin-bottom: 20px;
  }
  &__questions-text {
    max-width: 645px;
    width: 100%;
    margin: 0 auto 38px;
  }
}
