.custom-box {
  position: relative;
  margin-left: -180px;
  margin-right: -180px;
  padding: 60px 177px 60px 70px;
  display: flex;
  align-items: center;
  background: #f8f8f8;
  border-radius: 10px;
  @include response(1200px) {
    margin-left: 0;
    margin-right: 0;
    padding: 60px 20px;
    flex-direction: column;
  }
  @include response(767px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  &__img {
    flex: 0 0 auto;
    margin-right: 48px;
    @include response(1200px) {
      margin-right: 0;
      margin-bottom: 20px;
    }
    .text-content & {
      img {
        box-shadow: none;
      }
    }
  }
  &__info {
    position: relative;
    flex: 1 1 auto;
  }
}
