.new-first {
  position: relative;
  padding-top: 154px;
  padding-bottom: 154px;
  padding: 154px 20px;
  @include response(1200px) {
    padding-top: 110px;
    padding-bottom: 100px;
  }
  &__title {
    position: relative;
    margin: 0 0 30px;
    font-weight: bold;
    font-size: 40px;
    line-height: 140%;
    text-align: center;
    color: #fff;
    @include response(1200px) {
      font-size: 28px;
      line-height: 160%;
      margin-bottom: 20px;
    }
    span {
      color: #f15a89;
    }
  }
  &__btn-box {
    text-align: center;
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
