.featured {
  position: relative;
  &__top {
    padding: 5px;
    position: relative;
    font-weight: bold;
    font-size: 14px;
    line-height: 160%;
    color: #fff;
    text-align: center;
    background: #252525;
  }
  &__list {
    display: flex;
    justify-content: center;
    background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
    @include response(1200px) {
      flex-wrap: wrap;
    }
  }
  &__item {
    position: relative;
    flex: 0 0 290px;
    max-width: 290px;
    width: 290px;
    height: 117px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    svg {
      max-width: 100%;
      max-height: 100%;
      width: auto;
    }
    @include response(1200px) {
      flex: 0 0 50%;
      max-width: 50%;
      width: 50%;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        height: 1px;
        background: #fff;
        mix-blend-mode: soft-light;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      width: 1px;
      background: #fff;
      mix-blend-mode: soft-light;
    }
  }
}
