.types {
  position: relative;
  &__list {
    position: relative;
  }
  &__item {
    position: relative;
    margin-bottom: 25px;
    background: #fff;
    border: 1px solid #e9e9e9;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 50px;
    @include response(1200px) {
      padding: 30px;
    }
  }
  &__top {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    @include response(767px) {
      display: block;
    }
  }
  &__img {
    flex: 0 0 400px;
    max-width: 400px;
    width: 100%;
    margin-right: 50px;
    @include response(1200px) {
      margin-right: 20px;
    }
    @include response(767px) {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 25px;
    }
    img {
      box-shadow: none;
      width: 100%;
    }
  }
  &__top-info {
    position: relative;
    flex: 1 1 auto;
  }
  &__title {
    font-size: 26px;
    line-height: 160%;
    color: #333;
    margin-bottom: 25px;
  }
  &__list-text {
    font-size: 14px;
    line-height: 160%;
    color: #333;
    .text-content & {
      li {
        padding-left: 0;
        &::before {
          display: none;
        }
      }
    }
    li {
      display: flex;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 20px;
    }
  }
  &__associated {
    margin-bottom: 25px;
    background: #f8f8f8;
    border-radius: 10px;
    font-size: 16px;
    line-height: 160%;
    color: #333;
    padding: 12px 25px;
  }
  &__text {
    position: relative;
    margin-bottom: 30px;
  }
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__btn {
    flex: 0 0 auto;
    background: #fff;
    border: 2px solid #e9e9e9;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
    display: flex;
    align-items: center;
    padding: 17px 30px;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    text-transform: uppercase;
    text-decoration: none;
    color: #333 !important;
    @include response(767px) {
      padding: 17px;
      font-size: 15px;
      margin-bottom: 20px;
    }
    @include desktop-hover() {
      border-color: #6c41bf;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 15px;
    }
    span {
      color: #6c41bf;
      text-decoration: underline;
      text-transform: none;
      margin-left: 4px;
    }
  }
  &__imgs {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    @include response(767px) {
      margin-left: -15px;
    }
    img {
      margin: 0 0 0 15px;
      box-shadow: none;
    }
  }
  &__bottom-text {
    padding-right: 40px;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #a6b6b6;
    @include response(767px) {
      padding-right: 0;
      padding-bottom: 20px;
    }
    span {
      color: #000;
    }
  }
}
