.buy-with {
  position: relative;
  background: #fff;
  border: 1px solid #e9e9e9;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 50px 50px 40px;
  @include response(1200px) {
    padding: 35px 25px;
  }
  &__title {
    margin-bottom: 25px;
    font-size: 20px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
  }
  &__list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-left: -27px;
  }
  &__img {
    position: relative;
    flex: 0 0 calc(33.333% - 27px);
    max-width: calc(33.333% - 27px);
    width: 100%;
    margin-left: 27px;
    margin-bottom: 22px;
    border: 1px solid #e9e9e9;
    border-radius: 10px;
    &:last-child {
      &::before {
        display: none;
      }
    }
    &::before {
      content: "+";
      position: absolute;
      font-size: 18px;
      color: #333;
      font-weight: 500;
      right: -20px;
      top: 50%;
      margin-top: -9px;
    }
    img {
      width: 100%;
      border-radius: 10px;
    }
  }
  &__checkbox-list {
    position: relative;
    padding-bottom: 27px;
  }
  &__checkbox-row {
    margin-bottom: 15px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    padding-left: 40px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__checkbox {
    position: relative;
    cursor: pointer;
  }
  &__checkbox-input {
    display: none;
    &:checked {
      & + .buy-with__checkbox-text {
        &::before {
          background: #6c41bf;
          border-color: #6c41bf;
        }
      }
    }
  }
  &__checkbox-text {
    display: block;
    position: relative;
    font-size: 14px;
    line-height: 24px;
    color: #333;
    padding-left: 40px;
    margin-left: -40px;
    font-weight: 500;
    flex: 0 0 auto;
    margin-right: 10px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 24px;
      height: 24px;
      border-radius: 50%;
      border: 1px solid #a6b6b6;
      transition: all 0.3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      width: 10px;
      height: 6px;
      border-bottom: 2px solid #fff;
      border-left: 2px solid #fff;
      transform: rotate(-45deg);
      left: 7px;
      top: 8px;
    }
  }
  &__checkbox-price {
    flex: 0 0 auto;
    font-size: 14px;
    color: #f15a89;
    font-weight: 500;
  }
  &__select {
    flex: 0 0 auto;
    margin-right: 10px;
    .select2-container .select2-selection--single .select2-selection__rendered {
      display: block;
      border: 1px solid #e9e9e9;
      border-radius: 5px;
      height: 32px;
      line-height: 30px;
      padding-left: 12px;
      padding-right: 32px;
      font-weight: 500;
    }
    .select2-container--default .select2-selection--single .select2-selection__arrow {
      width: 32px;
    }
    .select2-container--default .select2-selection--single .select2-selection__arrow b {
      border-bottom-color: #808080;
      border-left-color: #808080;
    }
    .select2-dropdown {
      padding-top: 0;
      padding-bottom: 0;
    }
    .select2-results {
      margin-left: 0;
      margin-right: 0;
    }
  }
  &__bottom {
    margin-left: -50px;
    margin-right: -50px;
    padding-left: 50px;
    padding-right: 50px;
    border-top: 1px solid #e9e9e9;
    padding-top: 20px;
    @include response(1200px) {
      margin-left: -25px;
      margin-right: -25px;
      padding-left: 25px;
      padding-right: 25px;
      text-align: right;
    }
  }
  &__bottom-row {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 6px;
    @include response(1200px) {
      justify-content: flex-end;
    }
  }
  &__bottom-row-left {
    position: relative;
    padding-right: 10px;
    display: flex;
    align-items: center;
    @include response(1200px) {
      padding-right: 0;
    }
  }
  &__total-title {
    font-size: 14px;
    line-height: 180%;
    color: #333;
    font-weight: 500;
    flex: 0 0 auto;
  }
  &__total-new {
    color: #f15a89;
    font-size: 18px;
    font-weight: 700;
    flex: 0 0 auto;
    margin-left: 10px;
  }
  &__total-old {
    flex: 0 0 auto;
    margin-left: 10px;
    font-size: 14px;
    line-height: 160%;
    text-decoration-line: line-through;
    color: #333;
    font-weight: 500;
  }
  &__btn {
    flex: 0 0 auto;
  }
  &__descriptions {
    font-size: 12px;
    line-height: 160%;
    color: #808080;
    max-width: 151px;
    width: 100%;
    font-weight: 500;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 15px;
    }
  }
}
