.list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  &__item {
    flex: 0 0 33.333%;
    max-width: 33.333%;
    width: 100%;
    background: #fff;
    border: 1px solid #e9e9e9;
    position: relative;
    text-align: center;
    padding: 17px 70px;
    font-size: 14px;
    line-height: 160%;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 80px;
    color: #333;
    @include response(767px) {
      flex: 0 0 50%;
      max-width: 50%;
      padding: 15px 15px 15px 40px;
      font-size: 12px;
    }
    svg {
      position: absolute;
      left: 25px;
      top: 50%;
      margin-top: -12px;
      @include response(767px) {
        left: 10px;
      }
    }
  }
}
.list2 {
  position: relative;
  &__item {
    margin-bottom: 30px;
    display: flex;
    &:last-child {
      margin-bottom: 0;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 25px;
      margin-top: 2px;
    }
  }
}
.list3 {
  position: relative;
  &__item {
    margin-bottom: 25px;
    display: flex;
    padding-top: 12px;
    span {
      margin-top: -12px;
      flex: 0 0 48px;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 25px;
      font-weight: bold;
      font-size: 24px;
      line-height: 160%;
      color: #6c41bf;
      text-align: center;
    }
  }
}
