.questions {
  position: relative;
  padding-top: 40px;
  border-top: 1px solid #e6e6e6;
  &__title {
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
  }
  &__list {
    position: relative;
  }
  &__item {
    position: relative;
    padding-top: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid #e6e6e6;
    &:last-child {
      border-bottom: 0;
    }
  }
  &__head {
    position: relative;
    margin-bottom: 25px;
    display: flex;
    align-items: center;
    @include response(1200px) {
      align-items: flex-start;
      margin-bottom: 35px;
    }
  }
  &__head-icon {
    flex: 0 0 48px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 25px;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #6c41bf;
    background: #f7edff;
  }
  &__head-title {
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    @include response(767px) {
      padding-top: 10px;
    }
  }
  &__body {
    position: relative;
    display: flex;
  }
  &__body-icon {
    position: relative;
    flex: 0 0 48px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: #fff;
    border: 1px solid #e6e6e6;
    margin-right: 32px;
    font-weight: bold;
    font-size: 20px;
    @include response(1200px) {
      position: absolute;
      top: -24px;
      left: 0;
      z-index: 1;
    }
  }
  &__answer {
    padding: 30px 40px;
    background: #f7edff;
    border-radius: 10px;
    position: relative;
    .text-content & {
      p {
        margin-top: 0;
      }
    }
    @include response(1200px) {
      border-radius: 50px 10px 10px;
      padding: 40px 30px;
    }
  }
  &__answer-arrow {
    position: absolute;
    right: calc(100% - 3px);
    top: 15px;
    @include response(1200px) {
      display: none;
    }
  }
}
