.stories {
  position: relative;
  background: #f8f8f8;
  padding-top: 75px;
  padding-bottom: 75px;
  margin-bottom: 50px;
  @include response(1200px) {
    margin-bottom: 35px;
  }
  &__decor1 {
    position: absolute;
    bottom: 0;
    left: 0;
    @include response(1200px) {
      display: none;
    }
  }
  &__decor2 {
    position: absolute;
    top: 0;
    right: 0;
    @include response(1200px) {
      display: none;
    }
  }
  &__title {
    margin-bottom: 50px;
  }
  &__slider {
    position: relative;
    margin-left: -10px;
    margin-right: -10px;
    @include response(1200px) {
      padding-left: 27px;
      padding-right: 27px;
    }
    .slick-slide {
      margin-left: 10px;
      margin-right: 10px;
    }
    .slick-arrow {
      position: absolute;
      display: flex;
      z-index: 1;
      cursor: pointer;
      top: 50%;
      margin-top: -41px;
      @include response(1200px) {
        height: 150px;
        width: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #f0f0f0;
        margin-top: -110px;
      }
      @include desktop-hover() {
        svg {
          fill: $purple;
        }
      }
      svg {
        fill: #a6b6b6;
      }
    }
    .slick-prev {
      left: -82px;
      @include response(1200px) {
        left: -15px;
      }
    }
    .slick-next {
      right: -82px;
      @include response(1200px) {
        right: -15px;
      }
      svg {
        transform: scaleX(-1);
      }
    }
    .slick-dots {
      @include response(1200px) {
        padding-top: 30px;
      }
    }
  }
  &__item {
    position: relative;
    background: #fff;
    border-radius: 5px;
    padding: 15px 15px 38px;
  }
  &__item-img {
    display: block;
    overflow: hidden;
    border-radius: 5px;
    margin-bottom: 30px;
    img {
      width: 100%;
    }
  }
  &__item-rate {
    margin-bottom: 15px;
  }
  &__item-name {
    margin-bottom: 25px;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: #6c41bf;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  &__item-text {
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: #333;
    height: 130px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    padding-left: 25px;
    padding-right: 25px;
    @include response(767px) {
      padding-left: 0;
      padding-right: 0;
      max-height: 130px;
      height: auto;
    }
    p {
      margin: 0;
    }
  }
}
