.chapters-new {
  position: relative;
  &__item {
    position: relative;
    padding-top: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid #e6e6e6;
    &:last-child {
      border-bottom: 0;
    }
  }
  &__item-head {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    background: #fff;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    text-decoration: none;
    @include desktop-hover() {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.1);
      .chapters-new__item-decor {
        fill: #6c41bf;
      }
    }
    @include response(767px) {
      display: block;
    }
  }
  &__item-img {
    flex: 0 0 269px;
    max-width: 269px;
    width: 100%;
    @include response(767px) {
      max-width: 100%;
    }
    img {
      width: 100%;
    }
  }
  &__item-holder {
    position: relative;
    flex: 1 1 auto;
    padding: 15px 15px 15px 40px;
    align-self: stretch;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include response(767px) {
      padding: 30px;
      align-items: flex-start;
    }
  }
  &__item-decor {
    position: absolute;
    top: 15px;
    right: 15px;
    fill: #e6e6e6;
    @include response(767px) {
      top: auto;
      bottom: 30px;
      right: 30px;
    }
  }
  &__item-top {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
    @include response(767px) {
      display: block;
    }
  }
  &__item-title {
    font-size: 24px;
    line-height: 160%;
    color: #333;
  }
  &__item-btn {
    flex: 0 0 auto;
    @include response(767px) {
      display: inline-block;
    }
  }
  &__item-line {
    width: 60px;
    height: 5px;
    border-radius: 5px;
    margin-bottom: 15px;
  }
  &__item-descriptions {
    color: #333;
    max-width: 323px;
    width: 100%;
    font-weight: 400;
    @include response(767px) {
      margin-bottom: 30px;
    }
  }
  &__item-text {
    padding-left: 60px;
    padding-right: 60px;
    @include response(767px) {
      padding-left: 0;
      padding-right: 0;
    }
  }
}
