.text-content {
  position: relative;
  font-family: "Gotham", sans-serif;
  font-size: 16px;
  line-height: 160%;
  font-weight: 400;
  color: #333;
  p {
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  hr {
    border: 0;
    height: 1px;
    background: #d7d7d7;
  }
  ol {
    list-style: none;
    padding: 0;
    counter-reset: myCounter;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    > li {
      margin-bottom: 20px;
      position: relative;
      padding-left: 36px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        counter-increment: myCounter;
        content: counter(myCounter) ".";
        position: absolute;
        left: 0;
        top: 0;
        color: $purple;
        font-weight: 700;
      }
    }
    ul {
      counter-reset: none;
      &:first-child {
        margin-top: 10px;
      }
      li {
        @include response(767px) {
          padding-left: 20px;
        }
        &::before {
          background: transparent;
          border: 1px solid #6c41bf;
        }
      }
    }
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 20px;
      position: relative;
      padding-left: 36px;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 8px;
        width: 10px;
        height: 10px;
        background: #6c41bf;
        border-radius: 50%;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    ul {
      li {
        @include response(767px) {
          padding-left: 20px;
        }
        &::before {
          background: transparent;
          border: 1px solid #6c41bf;
        }
      }
    }
  }
  iframe {
    border: 0;
    max-width: 100%;
    vertical-align: top;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @include response(767px) {
      width: 100%;
      height: 300px;
    }
  }
  h1 {
    font-size: 36px;
    line-height: 120%;
    font-weight: 700;
    margin: 10px 0 20px;
    @include response(767px) {
      font-size: 26px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h2 {
    font-size: 28px;
    line-height: 140%;
    margin: 10px 0 20px;
    font-weight: 700;
    @include response(767px) {
      font-size: 22px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h3 {
    font-size: 22px;
    line-height: 140%;
    font-weight: 700;
    margin: 10px 0 20px;
    @include response(767px) {
      font-size: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h4 {
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
  }
  h5 {
    margin: 10px 0 20px;
    font-size: 16px;
    line-height: 160%;
    font-weight: 700;
    position: relative;
    padding-left: 40px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &::before {
      content: "?";
      position: absolute;
      left: 0;
      top: -3px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      background: #f7edff;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      text-align: center;
      line-height: 1;
      color: #6c41bf;
    }
  }
  img {
    display: inline-block;
    vertical-align: top;
    margin: 10px 0 20px;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.07);
    border-radius: 10px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: $purple;
    font-weight: 500;
    @include response(767px) {
      word-wrap: break-word;
    }
    @include desktop-hover() {
      text-decoration: none;
    }
    img {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    }
  }
}

.bg {
  display: block;
  margin: 10px 0 20px;
  background: #f8f8f8;
  border-radius: 5px;
  padding: 50px 40px;
}

.bg2 {
  display: block;
  margin: 10px 0 20px;
  padding: 40px;
  background: #f7edff;
  border-radius: 5px;
  @include response(767px) {
    padding: 25px;
  }
}
.bg3 {
  background: #f8f8f8;
  border-radius: 10px;
  position: relative;
  margin-left: -180px;
  margin-right: -180px;
  padding: 60px 180px;
}
.bg4 {
  background: #f7edff;
  border-radius: 10px;
  padding: 50px;
  margin-bottom: 25px;
  @include response(767px) {
    padding: 30px;
  }
  &--2 {
    background: #f8f8f8;
  }
  &--3 {
    background: #fffbf9;
  }
}
.box {
  padding: 29px 40px;
  background: #fff;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  @include response(767px) {
    padding: 15px;
  }
}
.cust {
  position: relative;
  padding: 50px;
  background: rgba(255, 169, 184, 0.1);
  border: 1px solid #ffa9b8;
  border-radius: 10px;
  @include response(767px) {
    padding: 30px;
  }
  svg {
    vertical-align: middle;
  }
}
ol.padding-left {
  padding-left: 35px;
  @include response(767px) {
    padding-left: 0;
  }
  li {
    padding-left: 27px;
  }
}
ul.padding-left {
  padding-left: 35px;
  @include response(767px) {
    padding-left: 0;
  }
}

ul.list-img {
  li {
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;
    padding-left: 0;
    position: relative;
    &::before {
      content: "";
      width: 1px;
      background: #f7edff;
      top: 0;
      height: calc(100% + 40px);
      left: 25px;
      border-radius: 0;
      @include response(767px) {
        display: none;
      }
    }
    &::after {
      content: "";
      position: absolute;
      width: 62px;
      height: 62px;
      border-radius: 50%;
      left: -6px;
      top: -6px;
      border: 6px solid #fff;
      @include response(767px) {
        display: none;
      }
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
    @include response(767px) {
      display: block;
    }
    > span {
      flex: 0 0 50px;
      max-width: 50px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 25px;
      background: #f7edff;
      position: relative;
    }
    img {
      margin: 0;
    }
  }
}

ul.img-list-2 {
  max-width: 670px;
  li {
    display: flex;
    align-items: center;
    padding: 20px 6px;
    border: 1px solid #f7edff;
    border-radius: 10px;
    margin-bottom: 10px;
    &::before {
      display: none;
    }
    > span {
      width: 100px;
      flex: 0 0 100px;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        margin: 0;
      }
    }
    > div {
      span {
        font-size: 14px;
        display: block;
      }
    }
  }
}
.list-col-2 {
  display: flex;
  flex-wrap: wrap;
  li {
    flex: 0 0 50%;
    @include response(767px) {
      flex: 0 0 100%;
    }
  }
}
.list-col-3 {
  display: flex;
  flex-wrap: wrap;
  li {
    flex: 0 0 33.333%;
    @include response(767px) {
      flex: 0 0 100%;
    }
  }
}
.list-col-4 {
  display: flex;
  flex-wrap: wrap;
  li {
    flex: 0 0 25%;
    @include response(767px) {
      flex: 0 0 100%;
    }
  }
}

.icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: #f7edff;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  text-align: center;
  line-height: 1;
  margin-right: 10px;
  color: #6c41bf;
}
