.list-stones {
  position: relative;
  display: flex;
  @include response(767px) {
    flex-wrap: wrap;
  }
  &__item {
    flex: 0 0 25%;
    max-width: 25%;
    width: 100%;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 22px 10px;
    border: 1px solid #e9e9e9;
    @include response(767px) {
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
  &__item-img {
    flex: 0 0 36px;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 15px;
  }
  &__item-text {
    font-size: 14px;
    line-height: 160%;
    text-transform: uppercase;
    color: #333;
    font-weight: 500;
  }
}
