.browse-chapters {
  position: relative;
  @include response(767px) {
    padding-bottom: 30px;
  }
  &__title {
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #a6b6b6;
    margin-bottom: 2px;
    padding-left: 50px;
    @include response(767px) {
      padding-left: 0;
    }
  }
  &__text {
    padding-left: 50px;
    font-size: 16px;
    line-height: 160%;
    color: #a6b6b6;
    margin-bottom: 25px;
    @include response(767px) {
      padding-left: 0;
    }
  }
  &__box {
    background: #fff;
    border: 1px solid #e9e9e9;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    overflow: hidden;
    padding-top: 50px;
    display: flex;
    justify-content: space-between;
    @include response(767px) {
      display: block;
    }
  }
  &__holder {
    flex: 0 0 387px;
    max-width: 387px;
    width: 100%;
    @include response(767px) {
      max-width: 100%;
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .browse-chapters__head {
      .browse-chapters__cell {
        font-weight: bold;
        font-size: 20px;
        line-height: 160%;
        color: #333;
        @include response(767px) {
          padding-left: 30px;
        }
      }
    }
    &:nth-child(1) {
      .browse-chapters__head {
        .browse-chapters__cell {
          padding-left: 50px;
          @include response(767px) {
            padding-left: 30px;
          }
        }
      }
    }
  }
  &__head {
    margin-bottom: 40px;
    @include response(767px) {
      display: block;
    }
  }
  &__cell {
    position: relative;
  }
  &__end {
    font-size: 16px;
    line-height: 160%;
    padding-right: 57px;
    padding-top: 20px;
    @include response(767px) {
      padding-right: 20px;
      padding-top: 0;
      padding-left: 20px;
    }
    p {
      margin: 10px 0 25px;
    }
  }
  &__link {
    display: inline-flex;
    align-items: center;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #6c41bf;
    text-decoration: none;
    @include response(767px) {
      margin-left: 20px;
      margin-right: 20px;
      margin-bottom: 20px;
    }
    @include desktop-hover() {
      span {
        text-decoration: none;
      }
    }
    span {
      text-decoration: underline;
    }
    svg {
      flex: 0 0 auto;
      margin-left: 10px;
    }
  }
  &__body {
    position: relative;
  }
  &__disabled {
    padding-left: 50px;
    font-size: 16px;
    line-height: 160%;
    color: #a6b6b6;
    @include response(767px) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  &__list {
    position: relative;
  }
  &__item {
    display: flex;
    align-items: center;
    border: 1px solid #e9e9e9;
    padding: 27px 35px;
    margin-bottom: -1px;
    color: #333;
    text-decoration: none;
    @include response(767px) {
      margin-left: -1px;
    }
    @include desktop-hover() {
      text-decoration: underline;
    }
    .browse-chapters__holder:nth-child(1) & {
      margin-left: -1px;
    }
    .browse-chapters__holder:nth-child(2) & {
      margin-right: -1px;
    }
  }
  &__item-title {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    flex: 1 1 auto;
    padding-right: 10px;
  }
  &__item-number {
    padding: 3px 10px;
    font-weight: bold;
    font-size: 12px;
    line-height: 160%;
    text-align: center;
    border-radius: 5px;
    flex: 0 0 auto;
  }
}
