.list-text {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  &__item {
    flex: 0 0 25%;
    max-width: 25%;
    padding: 29px 10px;
    text-align: center;
    font-size: 14px;
    line-height: 160%;
    text-transform: uppercase;
    color: #333;
    border: 1px solid #e9e9e9;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    &--20 {
      flex: 0 0 20%;
      max-width: 20%;
    }
    @include response(767px) {
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
}
