.start {
  position: relative;
  padding-top: 25px;
  text-align: center;
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    font-weight: 500;
    svg {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__link {
    display: inline-flex;
    align-items: center;
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 160%;
    color: #6c41bf;
    @include desktop-hover() {
      .start__link-text {
        text-decoration: none;
      }
    }
    svg {
      flex: 0 0 auto;
      margin-left: 10px;
    }
  }
  &__link-text {
    text-decoration: underline;
    flex: 0 0 auto;
  }
}
