.recommended {
  position: relative;
  padding-top: 100px;
  padding-bottom: 138px;
  background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 165px;
    background: linear-gradient(357.74deg, #ba58e1 7.24%, #6f49bc 92.95%);
  }
  &__decor1 {
    position: absolute;
    left: 0;
    bottom: 108px;
    @include response(1200px) {
      display: none;
    }
  }
  &__decor2 {
    position: absolute;
    right: 0;
    top: 8px;
    @include response(1200px) {
      display: none;
    }
  }
  &__decor3 {
    position: absolute;
    bottom: 0;
    right: 162px;
    @include response(767px) {
      right: 50%;
      transform: translateX(1150px);
      max-width: none;
    }
  }
  &__title {
    max-width: 420px;
    width: 100%;
    margin: 0 auto 50px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &.title {
      color: #fff;
    }
  }
  &__text {
    max-width: 823px;
    width: 100%;
    margin: 0 auto 75px;
    font-size: 18px;
    line-height: 160%;
    text-align: center;
    color: #fff;
    @include response(1200px) {
      margin-bottom: 40px;
    }
  }
  &__list {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    @include response(1200px) {
      display: block;
    }
  }
  &__item {
    position: relative;
    text-align: center;
    margin-left: 25px;
    margin-right: 25px;
    @include response(1200px) {
      margin: 0 auto 30px;
      padding-bottom: 30px;
      border-bottom: 1px solid rgba(#f7edff, 0.3);
    }
    &--small {
      flex: 0 0 400px;
      max-width: 400px;
      width: 100%;
      @include response(1200px) {
        max-width: 450px;
      }
    }
    &--big {
      flex: 0 0 450px;
      max-width: 450px;
      width: 100%;
    }
  }
  &__item-label {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: top;
    font-weight: bold;
    font-size: 12px;
    line-height: 160%;
    text-align: center;
    color: #333;
    padding: 5px 12px;
    border-radius: 15px;
    margin-bottom: 15px;
    svg {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__item-body {
    background: #fff;
    border: 1px solid #e9e9e9;
    border-radius: 5px;
    padding: 25px 25px 40px;
  }
  &__item-img {
    margin-bottom: 25px;
    text-align: center;
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    img {
      width: 100%;
    }
  }
  &__item-title {
    margin: 0 0 25px;
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    text-align: center;
    color: #333;
    @include response(1200px) {
      font-size: 20px;
      br {
        display: none;
      }
    }
  }
  &__item-row-list {
    position: relative;
    margin-bottom: 25px;
  }
  &__item-row-title {
    margin-bottom: 5px;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #333;
  }
  &__item-row {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
    text-align: left;
    padding-left: 15px;
    padding-right: 15px;
    @include response(1200px) {
      font-size: 16px;
    }
    &--center {
      text-align: center;
      justify-content: center;
      font-size: 16px;
      padding-bottom: 20px;
      border-bottom: 1px solid #e6e6e6;
    }
    &:last-child {
      margin-bottom: 0;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 20px;
    }
  }
  &__item-sizes-title {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    font-size: 13px;
    line-height: 160%;
    color: #a6b6b6;
    svg {
      flex: 0 0 auto;
      margin-right: 6px;
    }
  }
  &__item-sizes-list {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    line-height: 160%;
    color: #333;
    margin-bottom: 25px;
    font-weight: 500;
  }
  &__item-sizes-item {
    flex: 0 0 48px;
    max-width: 48px;
    width: 48px;
    height: 48px;
    margin-left: 10px;
    margin-right: 10px;
    background: #f8f8f8;
    border: 1px solid #a6b6b6;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    text-transform: uppercase;
  }
  &__item-price {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
  }
  &__item-price-new {
    flex: 0 0 auto;
    margin-right: 8px;
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #f15a89;
  }
  &__item-price-old {
    font-size: 20px;
    line-height: 160%;
    text-decoration-line: line-through;
    color: #a6b6b6;
    font-weight: 500;
  }
  &__item-info {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 25px;
    font-size: 14px;
    line-height: 160%;
    color: #f15a89;
    font-weight: 500;
    svg {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__item-btn-box {
    position: relative;
  }
  &__item-btn {
    position: relative;
  }
  &__bottom {
    max-width: 520px;
    width: 100%;
    margin: 0 auto;
    background: rgba(255, 255, 255, 0.1);
    border: 2px solid #fff;
    border-radius: 5px;
    padding: 50px;
  }
  &__bottom-text {
    margin-bottom: 25px;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: #fff;
  }
  &__bottom-btn-box {
    text-align: center;
    position: relative;
  }
  &__bottom-btn {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 11px 31px;
    text-decoration: none;
    background: #fff;
    border-radius: 5px;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #6c41bf;
    border: 2px solid #fff;
    @include desktop-hover() {
      background: transparent;
      color: #fff;
      svg {
        fill: #fff;
      }
    }
    svg {
      fill: #6c41bf;
      flex: 0 0 auto;
      margin-left: 10px;
    }
  }
  &__drop {
    position: relative;
    margin-bottom: 25px;
    background: #f8f8f8;
    border-radius: 5px;
    text-align: left;
  }
  &__drop-current {
    position: relative;
    padding: 25px 40px;
    cursor: pointer;
    @include response(767px) {
      padding-left: 30px;
      padding-right: 30px;
    }
    @include desktop-hover() {
      .recommended__drop-text-val {
        text-decoration: underline;
      }
    }
  }
  &__drop-current-inner {
    display: flex;
    align-items: center;
  }
  &__drop-val {
    flex: 0 0 46px;
    max-width: 46px;
    height: 46px;
    width: 100%;
    background: #fff;
    border: 1px solid #a6b6b6;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: #333;
    font-weight: 700;
    margin-right: 17px;
    span {
      font-size: 13px;
      flex: 0 0 auto;
      font-weight: 500;
    }
  }
  &__drop-arrow {
    position: absolute;
    right: 40px;
    top: 50%;
    margin-top: -3px;
    @include response(767px) {
      right: 30px;
    }
    .recommended__drop.active & {
      transform: scaleY(-1);
    }
  }
  &__drop-text-val {
    padding-top: 11px;
    padding-bottom: 10px;
    font-size: 15px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
    border-bottom: 1px solid #a6b6b6;
    flex: 1 1 auto;
    padding-right: 15px;
    span {
      color: #f15a89;
      font-weight: 700;
    }
  }
  &__drop-list {
    position: absolute;
    top: calc(100% - 25px);
    left: 0;
    right: 0;
    z-index: 1;
    background: #f8f8f8;
    padding: 15px 40px;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition: all 0.3s ease-in-out;
    @include response(767px) {
      padding-left: 30px;
      padding-right: 30px;
    }
    .recommended__drop.active & {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }
  &__drop-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    cursor: pointer;
    @include desktop-hover() {
      .recommended__drop-text-val {
        text-decoration: underline;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}
