.subscribe-new {
  position: relative;
  background: #f8f8f8;
  padding-top: 100px;
  padding-bottom: 100px;
  @include response(1200px) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &__left-img {
    position: absolute;
    bottom: 0;
    left: 0;
    @include response(1200px) {
      display: none;
    }
  }
  &__right-img {
    position: absolute;
    bottom: 0;
    right: 0;
    @include response(1200px) {
      display: none;
    }
  }
  &__box {
    position: relative;
    display: flex;
    align-items: center;
    @include response(1200px) {
      flex-direction: column;
    }
  }
  &__info {
    flex: 1 1 auto;
    position: relative;
    padding-right: 20px;
    @include response(1200px) {
      padding-right: 0;
      text-align: center;
      margin-bottom: 20px;
    }
  }
  &__title {
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #333;
  }
  &__text {
    font-size: 20px;
    line-height: 43px;
    font-weight: 500;
    span {
      color: #f15a89;
    }
  }
  &__form {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    @include response(666px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__form-input {
    flex: 0 0 300px;
    width: 300px;
    margin-right: 10px;
    background: #fff;
    border: 1px solid #d7d7d7;
    border-radius: 50px;
    padding-left: 28px;
    padding-right: 28px;
    font-size: 15px;
    line-height: 160%;
    outline: none;
    color: #000;
    transition: all 0.3s ease-in-out;
    @include response(666px) {
      flex: 0 0 auto;
      height: 52px;
      margin-bottom: 15px;
      text-align: center;
    }
    &:focus {
      border-color: #6c41bf;
    }
    &::placeholder {
      color: #808080;
    }
  }
  &__form-btn {
    border-radius: 50px;
    flex: 0 0 auto;
    @include response(666px) {
      width: 300px;
    }
    &::before {
      border-radius: 50px;
    }
  }
}
