.team {
  position: relative;
  padding-bottom: 20px;
  &__item {
    margin-bottom: 50px;
    display: flex;
    @include response(1200px) {
      display: block;
      max-width: 80%;
      width: 100%;
      margin: 0 auto 40px;
    }
    @include response(767px) {
      max-width: 100%;
    }
    &:nth-child(even) {
      flex-direction: row-reverse;
      .team__img {
        flex-direction: row-reverse;
        padding-right: 0;
      }
      .team__img-ava {
        margin-right: 0;
        margin-left: 42px;
        @include response(767px) {
          margin-left: 0;
        }
      }
      .team__item-text {
        padding-right: 35px;
        @include response(767px) {
          padding-right: 0;
        }
      }
    }
  }
  &__img {
    flex: 0 0 50%;
    padding-right: 10px;
    display: flex;
    @include response(1200px) {
      padding-right: 0;
      margin-bottom: 20px;
    }
    @include response(767px) {
      display: block;
    }
  }
  &__img-ava {
    flex: 0 0 250px;
    max-width: 250px;
    width: 100%;
    margin-right: 42px;
    border-radius: 15px;
    overflow: hidden;
    @include response(767px) {
      max-width: 100%;
      margin-bottom: 20px;
      > img {
        width: 100%;
      }
    }
  }
  &__img-box {
    flex: 1 1 auto;
    position: relative;
  }
  &__item-social {
    list-style: none;
    display: flex;
    margin: 0 0 15px;
    padding: 0;
    li {
      margin-right: 9px;
      &:last-child {
        margin-right: 0;
      }
    }
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      text-decoration: none;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      border: 2px solid #000;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        top: -2px;
        left: -2px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        transition: 0.3s ease-in-out;
        background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
        opacity: 0;
        z-index: 1;
        transform: scaleX(-1);
      }
      svg {
        fill: #000;
        position: relative;
        z-index: 1;
      }
      @include desktop-hover() {
        svg {
          fill: #fff;
        }
        &::before {
          opacity: 1;
        }
        opacity: 1;
      }
    }
  }
  &__item-name {
    font-size: 17px;
    text-transform: uppercase;
    line-height: 24px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  &__item-position {
    text-transform: uppercase;
    font-size: 12px;
    color: $purple;
  }
  &__item-text {
    flex: 0 0 50%;
    padding-bottom: 10px;
    border-bottom: 1px solid #000;
    @include response(1200px) {
      border-bottom: 0;
    }
  }
}
