.custom-box2 {
  position: relative;
  &__item {
    position: relative;
    margin-bottom: 25px;
    padding: 40px 40px 30px;
    display: flex;
    border-radius: 10px;
    @include response(767px) {
      padding: 30px;
      display: block;
    }
  }
  &__item-img {
    flex: 0 0 auto;
    margin-right: 40px;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 20px;
    }
    .text-content & {
      img {
        box-shadow: none;
      }
    }
  }
  &__item-box {
    position: relative;
    flex: 1 1 auto;
  }
  &__item-title {
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #333;
    margin-bottom: 25px;
    @include response(767px) {
      margin-bottom: 10px;
    }
  }
  &__item-text {
    font-size: 15px;
    line-height: 160%;
    margin-bottom: 25px;
    @include response(767px) {
      margin-bottom: 15px;
    }
  }
  &__item-info {
    position: relative;
    display: flex;
    @include response(767px) {
      flex-wrap: wrap;
    }
  }
  &__item-color {
    font-size: 15px;
    line-height: 160%;
    text-align: center;
    color: #fff;
    padding: 10px 15px;
    border-radius: 10px;
    flex: 0 0 auto;
    margin-right: 10px;
    @include response(767px) {
      margin-top: 10px;
    }
  }
  &__item-el {
    font-size: 15px;
    line-height: 160%;
    text-align: center;
    color: #000;
    padding: 10px 15px;
    border-radius: 10px;
    flex: 0 0 auto;
    border: 1px solid #e9e9e9;
    @include response(767px) {
      margin-top: 10px;
    }
  }
}
