.product-page {
  position: relative;
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    @include response(1200px) {
      flex-direction: column;
    }
    @include response(767px) {
      margin-bottom: 20px;
    }
    &--new {
      align-items: flex-start;
      @include response(1200px) {
        margin-bottom: 40px;
      }
    }
  }
  &__sliders {
    display: flex;
    align-items: center;
    margin-right: 36px;
    @include response(1200px) {
      margin-right: 0;
      border-bottom: 1px solid #f7edff;
      margin-bottom: 25px;
    }
  }
  &__sliders-new {
    position: relative;
    flex: 0 0 515px;
    max-width: 515px;
    width: 100%;
    margin-right: 76px;
    @include response(1200px) {
      max-width: 100%;
      flex: 0 0 auto;
      margin-right: 0;
      margin-bottom: 25px;
      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: -25px;
        right: -25px;
        height: 1px;
        background: #e9e9e9;
      }
    }
  }
  &__for-new {
    margin-bottom: 28px;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    overflow: hidden;
    @include response(767px) {
      margin-bottom: 15px;
    }
  }
  &__for-item-new {
    position: relative;
    vertical-align: top;
    img {
      width: 100%;
    }
  }
  &__for-zoom {
    position: absolute;
    bottom: 15px;
    right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 38px;
    height: 38px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    background: #fff;
    outline: none;
    @include desktop-hover() {
      background: $purple;
      svg {
        fill: #fff;
      }
    }
  }
  &__nav-new {
    padding-left: 45px;
    padding-right: 45px;
    margin-bottom: 50px;
    @include response(767px) {
      padding-left: 0;
      padding-right: 0;
      margin-bottom: 25px;
    }
    .slick-slide {
      margin-left: 5px;
      margin-right: 5px;
    }
    .slick-arrow {
      position: absolute;
      padding: 10px;
      top: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      &.slick-disabled {
        opacity: 0.3;
        pointer-events: none;
      }
    }
    .slick-prev {
      left: 10px;
      svg {
        transform: rotate(-90deg);
      }
    }
    .slick-next {
      right: 10px;
      svg {
        transform: rotate(90deg);
      }
    }
  }
  &__nav-new-item {
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    border: 1px solid #e6e6e6;
    height: 75px;
    vertical-align: top;
    @include response(767px) {
      height: 64px;
    }
    .slick-current & {
      border-color: $purple;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__nav {
    flex: 0 0 75px;
    max-width: 75px;
    width: 100%;
    margin-bottom: -10px;
    @include response(1200px) {
      display: none !important;
    }
    .slick-slide {
      margin-bottom: 10px;
    }
    .slick-arrow {
      position: absolute;
      left: 0;
      right: 0;
      padding: 10px;
      cursor: pointer;
      z-index: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        fill: none;
        stroke-width: 2;
        stroke: #000;
      }
      &.slick-next {
        bottom: -17px;
        svg {
          transform: scaleY(-1);
        }
      }
      &.slick-prev {
        bottom: 100%;
      }
    }
  }
  &__nav-item {
    vertical-align: top;
    border: 1px solid $purple;
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    opacity: 0.25;
    transition: 0.3s ease-in-out;
    cursor: pointer;
    .slick-current & {
      opacity: 1;
    }
  }
  &__for {
    position: relative;
    flex: 0 0 480px;
    max-width: 480px;
    width: 100%;
    @include response(1200px) {
      flex: 0 0 auto;
      max-width: 60vw;
    }
    @include response(767px) {
      max-width: calc(100vw - 50px);
    }
  }
  &__for-item {
    padding: 40px;
    vertical-align: top;
    border-radius: 10px;
    overflow: hidden;
    img {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      border-radius: 10px;
    }
    @include response(1200px) {
      padding: 0;
      img {
        width: 100%;
      }
    }
  }
  &__info {
    position: relative;
    flex: 1 1 auto;
    @include response(1200px) {
      max-width: 60vw;
      width: 100%;
      display: flex;
      flex-direction: column;
    }
    @include response(767px) {
      max-width: 100%;
    }
    &--new {
      @include response(1200px) {
        max-width: 100%;
      }
    }
  }
  &__info-head {
    position: relative;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(1200px) {
      display: none;
    }
  }
  &__info-rate {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    @include response(1200px) {
      display: none;
      &--new {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        .rate-counter--link {
          margin-top: 4px;
        }
      }
    }
  }
  &__title {
    font-weight: bold;
    font-size: 40px;
    line-height: 160%;
    color: #333;
    padding-bottom: 10px;
    border-bottom: 1px solid #f7edff;
    margin-bottom: 30px;
    @include response(1200px) {
      font-size: 20px;
      text-align: center;
      padding-bottom: 0;
      margin-bottom: 2px;
      border-bottom: 0;
    }
  }
  &__descriptions {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    margin-bottom: 35px;
    @include response(1200px) {
      &--new {
        margin-bottom: 30px;
      }
    }
  }
  &__select {
    position: relative;
    margin-bottom: 35px;
    max-width: 360px;
    @include response(1200px) {
      order: 1;
      max-width: 100%;
      margin-bottom: 15px;
    }
  }
  &__gift {
    background: #f8f8f8;
    border-radius: 10px;
    position: relative;
    margin-bottom: 35px;
    padding: 20px 30px;
    display: flex;
    align-items: center;
    @include response(1200px) {
      margin-bottom: 0;
      &--new {
        margin-bottom: 30px;
      }
    }
    @include response(767px) {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }
  &__gift-icon {
    flex: 0 0 auto;
    margin-right: 24px;
    @include response(767px) {
      margin-right: 19px;
    }
  }
  &__gift-text {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    @include response(390px) {
      font-size: 14px;
    }
  }
  &__controls {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      padding: 18px 25px;
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 2;
      margin-bottom: 0;
      background: #fff;
      .select2-style--2 .select2-container .select2-selection--single .select2-selection__rendered {
        height: 44px;
        line-height: 42px;
      }
    }
  }
  &__controls-left {
    position: relative;
    flex: 1 1 auto;
    padding-right: 20px;
    @include response(1200px) {
      display: none;
    }
  }
  &__price {
    display: flex;
    align-items: center;
    @include response(1200px) {
      &--new {
        justify-content: space-between;
        margin-bottom: 20px;
        .product-page__price-old {
          flex: 1 1 auto;
        }
      }
    }
  }
  &__price-descriptions {
    font-size: 13px;
    line-height: 160%;
    color: #a6b6b6;
    @include response(1200px) {
      text-align: center;
    }
  }
  &__controls-right {
    display: flex;
    align-items: center;
    flex: 0 0 auto;
    @include response(1200px) {
      flex: 1 1 auto;
    }
  }
  &__btn {
    flex: 0 0 auto;
    @include response(1200px) {
      flex: 1 1 auto;
    }
    @include response(767px) {
      height: 44px;
    }
  }
  &__price-new {
    flex: 0 0 auto;
    margin-right: 10px;
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #f15a89;
    @include response(1200px) {
      font-size: 20px;
    }
  }
  &__price-old {
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    text-decoration-line: line-through;
    color: #333333;
    flex: 0 0 auto;
    @include response(1200px) {
      font-size: 16px;
      margin-right: 10px;
    }
  }
  &__btn {
    margin-left: 10px;
  }
  &__content {
    position: relative;
    display: flex;
    align-items: flex-start;
    margin-bottom: 50px;
    @include response(1200px) {
      display: block;
      margin-bottom: 30px;
    }
  }
  &__content-aside {
    background: #f8f8f8;
    border-radius: 5px;
    position: relative;
    flex: 0 0 356px;
    max-width: 356px;
    width: 356px;
    padding-top: 75px;
    padding-bottom: 68px;
    margin-top: 80px;
    &--2 {
      padding-bottom: 40px;
      padding-top: 40px;
    }
    @include response(1200px) {
      max-width: 60vw;
      margin-top: 0;
      width: 100%;
      margin: 0 auto 30px;
    }
    @include response(767px) {
      max-width: 100%;
    }
  }
  &__content-aside-title {
    font-size: 20px;
    line-height: 160%;
    color: #333333;
    margin-bottom: 19px;
    padding-left: 76px;
    font-weight: 500;
    &--pl40 {
      padding-left: 40px;
    }
  }
  &__content-aside-list {
    list-style: none;
    padding: 0 0 35px;
    margin: 0 0 27px;
    border-bottom: 1px solid #ececec;
    li {
      font-size: 16px;
      padding-left: 76px;
      position: relative;
      padding-right: 10px;
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        content: "";
        position: absolute;
        left: 40px;
        top: 4px;
        width: 10px;
        height: 10px;
        background: #6c41bf;
        border-radius: 50%;
      }
    }
  }
  &__content-box {
    flex: 1 1 auto;
    margin-left: 20px;
    @include response(1200px) {
      margin-left: 0;
    }
  }
  &__options {
    position: relative;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    padding: 58px 75px 60px;
    @include response(1200px) {
      box-shadow: none;
      padding: 20px 0;
    }
  }
  &__content-text {
    position: relative;
    margin-bottom: 50px;
  }
  &__content-bottom {
    padding: 40px;
    background: #f7edff;
    border-radius: 5px;
    display: flex;
    align-items: center;
  }
  &__content-title {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    flex: 0 0 auto;
    margin-right: 5px;
  }
  &__content-link {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: $purple;
    @include desktop-hover() {
      text-decoration: none;
    }
    svg {
      fill: $purple;
      flex: 0 0 auto;
      margin-left: 5px;
    }
  }
  &__add-btn {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    @include response(1200px) {
      justify-content: center;
      &--new {
        justify-content: flex-start;
      }
    }
  }
  &__add-btn-icon {
    flex: 0 0 auto;
    margin-right: 6px;
    display: flex;
    svg {
      fill: #a6b6b6;
    }
  }
  &__add-btn-text {
    font-size: 13px;
    line-height: 160%;
    color: #a6b6b6;
  }
  &__checkbox {
    position: relative;
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    @include response(1200px) {
      margin-right: -40px;
    }
  }
  &__checkbox-cell {
    flex: 0 0 auto;
    margin-right: 40px;
    margin-bottom: 20px;
    @include response(1200px) {
      flex: 0 0 calc(50% - 40px);
      max-width: calc(50% - 40px);
    }
  }
  &__size {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 25px;
  }
  &__size-item {
    position: relative;
    display: flex;
    cursor: pointer;
    margin-right: 5px;
    margin-bottom: 5px;
    &.disabled {
      pointer-events: none;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__size-input {
    display: none;
    &:checked {
      & + .product-page__size-value {
        background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
        color: #fff;
        border-color: transparent;
        & + .product-page__size-hint {
          display: block;
        }
      }
    }
  }
  &__size-value {
    flex: 0 0 48px;
    width: 48px;
    height: 48px;
    border: 1px solid #a6b6b6;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    line-height: 1;
    text-transform: uppercase;
    text-align: center;
    color: #a6b6b6;
    font-weight: 500;
    position: relative;
    overflow: hidden;
    @include desktop-hover() {
      background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
      color: #fff;
      border-color: transparent;
    }
    .disabled & {
      &::before {
        content: "";
        position: absolute;
        width: 1px;
        height: 66px;
        transform: rotate(-45deg);
        transform-origin: 0 0;
        left: 0;
        top: 0;
        background: #a6b6b6;
      }
    }
    > span {
      font-size: 13px;
      text-transform: none;
    }
  }
  &__size-hint {
    display: none;
    margin-left: 5px;
    background: #f8f8f8;
    border-radius: 5px;
    padding: 7px 15px;
    font-weight: 500;
  }
  &__size-hint-title {
    display: block;
    font-size: 12px;
    color: #333;
    line-height: 140%;
  }
  &__size-hint-row {
    display: block;
    font-size: 12px;
    line-height: 140%;
    color: #333;
    span {
      color: #808080;
    }
  }
  &__mobile-head {
    display: none;
    @include response(1200px) {
      display: block;
      margin-bottom: 15px;
      position: relative;
    }
  }
  &__mobile-row {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__size-new {
    margin-bottom: 20px;
    position: relative;
    max-width: 360px;
  }
  &__size-new-current {
    position: relative;
    cursor: pointer;
    padding: 10px 20px;
    background: #f8f8f8;
    border-radius: 5px;
    @include desktop-hover() {
      .product-page__size-new-text-val {
        text-decoration: underline;
      }
    }
  }
  &__size-new-current-inner {
    position: relative;
    display: flex;
    align-items: center;
  }
  &__size-new-val {
    font-weight: bold;
    font-size: 14px;
    line-height: 160%;
    color: #6c41bf;
    position: relative;
    flex: 0 0 32px;
    min-width: 32px;
    height: 32px;
    border-radius: 99px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-right: 10px;
    background: #fff;
    overflow: hidden;
    &--sold {
      border: 1px solid #ffa9b8;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        bottom: 0;
        width: 1px;
        background: #ffa9b8;
        transform: rotate(-45deg);
      }
    }
    &--full {
      flex: 0 0 auto;
      padding-left: 8px;
      padding-right: 8px;
      width: auto;
      &::before {
        transform: rotate(15deg);
        width: 100%;
        left: 0;
        top: 50%;
        height: 1px;
      }
    }
    &--transparent {
      background: transparent;
    }
  }
  &__size-new-text-val {
    font-size: 14px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
    span {
      color: #f15a89;
      font-weight: 700;
    }
  }
  &__size-new-arrow {
    position: absolute;
    right: 20px;
    top: 50%;
    margin-top: -3px;
    .product-page__size-new.active & {
      transform: scaleY(-1);
    }
  }
  &__size-new-list {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 2;
    background: #f8f8f8;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition: all 0.3s ease-in-out;
    .product-page__size-new.active & {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }
  &__size-new-item {
    padding: 10px 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
    @include desktop-hover() {
      .product-page__size-new-text-val {
        text-decoration: underline;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}
