.info2 {
  position: relative;
  background: #f7edff;
  border-radius: 10px;
  padding-top: 60px;
  margin-bottom: 60px;
  @include response(1200px) {
    padding-top: 40px;
  }
  &__text {
    position: relative;
    margin-bottom: 37px;
    padding-left: 60px;
    padding-right: 60px;
    @include response(1200px) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  img {
    box-shadow: none !important;
  }
}
