.benefits {
  position: relative;
  padding-top: 75px;
  padding-bottom: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  @include response(767px) {
    display: block;
    padding-left: 20px;
    padding-right: 20px;
  }
  &__item {
    margin-left: 50px;
    margin-right: 50px;
    flex: 0 0 315px;
    max-width: 315px;
    width: 100%;
    display: flex;
    align-items: center;
    @include response(1200px) {
      margin-bottom: 36px;
    }
    @include response(767px) {
      width: 100%;
      max-width: 100%;
      margin-left: 0;
      margin-right: 0;
    }
  }
  &__item-img {
    flex: 0 0 125px;
    max-width: 125px;
    width: 100%;
    margin-right: 25px;
  }
  &__item-box {
    position: relative;
  }
  &__item-title {
    position: relative;
    font-weight: bold;
    font-size: 20px;
    line-height: 140%;
    color: #000;
    span {
      color: #6c41bf;
    }
  }
  &__item-text {
    margin-top: 5px;
    font-size: 12px;
    line-height: 160%;
    color: #757575;
  }
}
