.custom-section {
  position: relative;
  &__item {
    margin-bottom: 22px;
    background: #fff;
    border: 1px solid #e9e9e9;
    border-radius: 10px;
    padding: 40px 40px 30px;
  }
  &__head {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
  }
  &__img {
    flex: 0 0 auto;
    margin-right: 20px;
  }
  &__title {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333;
  }
  &__body {
    font-size: 14px;
    line-height: 180%;
  }
}
