.breadcrumb-mobile-box {
  display: none;
  @include response(1200px) {
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
  }
}
.breadcrumb {
  list-style: none;
  margin: 0;
  padding: 0 20px;
  display: inline-flex;
  flex-wrap: wrap;
  background: #f8f8f8;
  border-radius: 4px;
  @include response(767px) {
    padding-top: 5px;
    padding-bottom: 5px;
  }
  li {
    margin-right: 55px;
    &:last-child {
      margin-right: 0;
    }
  }
  a {
    display: block;
    position: relative;
    font-size: 12px;
    line-height: 19px;
    color: #333;
    text-decoration: none;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: left;
    @include response(767px) {
      padding-top: 5px;
      padding-bottom: 5px;
    }
    @include desktop-hover() {
      text-decoration: underline;
    }
    &::before {
      content: "";
      position: absolute;
      width: 24px;
      height: 1px;
      background: #808080;
      right: -40px;
      top: 19px;
      @include response(767px) {
        top: 15px;
      }
    }
  }
  span {
    display: block;
    font-size: 12px;
    line-height: 19px;
    color: #333;
    text-align: left;
    opacity: 0.5;
    padding-top: 10px;
    padding-bottom: 10px;
    @include response(767px) {
      padding-top: 5px;
      padding-bottom: 5px;
    }
  }
  &--new {
    padding: 0;
    a {
      font-weight: 500;
      font-size: 13px;
      line-height: 160%;
      color: #fff;
      padding: 6px 22px;
      background: $purple;
      border-radius: 4px;
      transform: perspective(1px);
      @include desktop-hover() {
        text-decoration: none;
        color: $purple;
        &::before {
          opacity: 1;
        }
      }
      &::before {
        width: auto;
        left: 2px;
        right: 2px;
        height: auto;
        border-radius: 4px;
        bottom: 2px;
        top: 2px;
        background: #fff;
        transition: 0.3s ease-in-out;
        opacity: 0;
        z-index: -1;
      }
    }
  }
}
