.trig-list {
  position: relative;
  display: flex;
  margin-bottom: 40px;
  @include response(767px) {
    flex-wrap: wrap;
    justify-content: center;
  }
  &__item {
    flex: 0 0 20%;
    max-width: 20%;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    @include response(767px) {
      flex: 0 0 50%;
      max-width: 50%;
      margin-bottom: 25px;
    }
  }
  &__item-img {
    height: 36px;
    position: relative;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;
    flex: 0 0 auto;
    @include response(767px) {
      .trig-list__item--more & {
        display: none;
      }
    }
    img {
      box-shadow: none;
    }
  }
  &__item-text {
    font-size: 14px;
    line-height: 160%;
    text-align: center;
    text-transform: uppercase;
    color: #333;
    padding: 17px 14px;
    border: 1px solid #e9e9e9;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    .trig-list__item--more & {
      background: #f8f8f8;
      color: #a6b6b6;
    }
  }
}
