.how-use {
  position: relative;
  background: linear-gradient(180deg, #f7edff 0%, #fdf8ff 100%);
  padding-top: 100px;
  padding-bottom: 75px;
  @include response(1200px) {
    padding-top: 75px;
  }
  &__title {
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__descriptions {
    max-width: 823px;
    width: 100%;
    margin: 0 auto 30px;
    font-size: 18px;
    line-height: 160%;
    text-align: center;
    color: #333;
    @include response(1200px) {
      margin-bottom: 40px;
    }
    span {
      font-weight: 500;
    }
  }
  &__size-list {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
  }
  &__size-item {
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 20px;
    margin-right: 20px;
    position: relative;
    &:last-child {
      .how-use__size-item-arrow {
        display: none;
      }
    }
  }
  &__size-title {
    font-weight: bold;
    font-size: 12px;
    line-height: 160%;
    text-align: center;
    color: #333;
    background: #fff;
    border: 1px solid #e6e6e6;
    border-radius: 15px;
    padding: 1px 12px;
    margin-bottom: 11px;
  }
  &__size-img {
    position: relative;
    height: 88px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__size-item-arrow {
    position: absolute;
    bottom: 37px;
    right: -32px;
  }
  &__steps-title {
    margin-bottom: 136px;
    font-size: 18px;
    line-height: 160%;
    text-align: center;
    color: #333;
    font-weight: 500;
    @include response(1200px) {
      margin-bottom: 36px;
    }
  }
  &__steps-list {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    @include response(1200px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__steps-item {
    flex: 0 0 360px;
    max-width: 360px;
    width: 100%;
    margin-left: 25px;
    margin-right: 25px;
    position: relative;
    padding: 0 40px 40px;
    @include response(1200px) {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &:nth-child(2) {
      margin-top: -83px;
      @include response(1200px) {
        margin-top: 0;
      }
    }
    &:nth-child(3) {
      margin-top: 50px;
      @include response(1200px) {
        margin-top: 0;
      }
    }
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      top: 50px;
      background: #fff;
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      border-radius: 5px;
    }
  }
  &__steps-item-img {
    position: relative;
    text-align: center;
    margin-bottom: 20px;
  }
  &__steps-item-row {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 23px;
  }
  &__steps-item-number {
    flex: 0 0 36px;
    max-width: 36px;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: #fff;
    margin-right: 15px;
  }
  &__steps-item-title {
    font-size: 20px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
  }
  &__steps-item-text {
    position: relative;
  }
}
