.blog-page {
  position: relative;
  &__img-decor {
    text-align: center;
    position: relative;
    margin-bottom: -35px;
    padding-top: 40px;
    @include response(767px) {
      margin-bottom: -20px;
    }
  }
  &__main {
    position: relative;
    margin-bottom: 76px;
    padding-top: 127px;
    @include response(1200px) {
      padding-top: 75px;
      padding-bottom: 75px;
      background: #f7edff;
      margin-bottom: 40px;
    }
  }
  &__main-img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    @include response(1200px) {
      display: none;
    }
  }
  &__main-mobile {
    display: none;
    @include response(1200px) {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__main-box {
    position: relative;
    background: #fff;
    padding: 50px;
    border: 1px solid #e9e9e9;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    @include response(1200px) {
      padding: 40px 30px;
    }
  }
  &__main-title {
    margin: 0 0 10px;
    font-weight: bold;
    font-size: 40px;
    line-height: 160%;
    text-align: center;
    color: #333;
    @include response(1200px) {
      font-size: 30px;
    }
  }
  &__main-text {
    position: relative;
    padding-top: 20px;
    max-width: 650px;
    width: 100%;
    margin: 0 auto 35px;
    text-align: center;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      width: 824px;
      left: 50%;
      transform: translateX(-50%);
      height: 1px;
      background: #f7edff;
    }
  }
  &__main-btns {
    display: flex;
    align-items: center;
    justify-content: center;
    @include response(1200px) {
      flex-direction: column;
    }
  }
  &__main-btn {
    position: relative;
    text-decoration: none;
    background: #fff;
    border: 2px solid #e9e9e9;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
    display: flex;
    align-items: center;
    padding: 17px 28px;
    margin-left: 8px;
    margin-right: 8px;
    transition: all 0.3s ease-in-out;
    color: #6c41bf;
    @include response(1200px) {
      margin-bottom: 20px;
    }
    @include desktop-hover() {
      background: #6c41bf;
      color: #fff;
      border-color: #6c41bf;
      svg {
        fill: #fff;
      }
    }
    svg {
      fill: #6c41bf;
      flex: 0 0 auto;
      margin-right: 15px;
    }
    span {
      flex: 0 0 auto;
      font-weight: bold;
      font-size: 16px;
      line-height: 160%;
      text-decoration-line: underline;
    }
  }
  &__main-btn-down {
    font-size: 16px;
    line-height: 160%;
    color: #757575;
    text-decoration: none;
    font-weight: bold;
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-left: 8px;
    margin-right: 8px;
    @include desktop-hover() {
      color: #6c41bf;
      svg {
        stroke: #6c41bf;
      }
    }
    svg {
      fill: none;
      stroke: #757575;
      flex: 0 0 auto;
      margin-left: 10px;
    }
    span {
      display: inline-block;
      vertical-align: middle;
      margin-right: 5px;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__all-articles {
    text-align: center;
    padding-bottom: 75px;
  }
  &__top-img {
    max-width: 960px;
    width: 100%;
    margin: 0 auto 5px;
    position: relative;
  }
  &__time {
    position: absolute;
    bottom: 10px;
    left: 10px;
    background: #fff;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    text-align: center;
    width: 60px;
    height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__time-icon {
    position: absolute;
    top: 4px;
    left: 4px;
  }
  &__time-value {
    font-size: 12px;
    color: #333;
    font-weight: 700;
  }
  &__time-text {
    font-size: 12px;
    color: #333;
  }
  &__box {
    max-width: 825px;
    width: 100%;
    margin: 0 auto;
  }
  &__bread {
    text-align: center;
    margin-bottom: 29px;
    @include response(767px) {
      margin-bottom: 15px;
    }
  }
  &__title {
    margin-bottom: 20px;
    @include response(767px) {
      &.title--fz40 {
        font-size: 24px;
      }
    }
  }
  &__row {
    position: relative;
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    @include response(767px) {
      margin-bottom: 20px;
    }
    &--mb23 {
      margin-bottom: 23px;
      @include response(1200px) {
        margin-bottom: 10px;
      }
    }
  }
  &__cell {
    flex: 0 0 auto;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    margin-left: 25px;
    margin-right: 25px;
    @include response(767px) {
      margin-left: 10px;
      margin-right: 10px;
    }
  }
  &__icon {
    flex: 0 0 auto;
    margin-right: 10px;
  }
  &__cell-text {
    flex: 0 0 auto;
    color: #a6b6b6;
    font-size: 13px;
    font-weight: 500;
    margin-right: 4px;
  }
  &__cell-text2 {
    flex: 0 0 auto;
    color: $purple;
    font-size: 13px;
    font-weight: 500;
    margin-right: 4px;
  }
  &__cell-text3 {
    flex: 0 0 auto;
    color: #333;
    font-size: 13px;
    font-weight: 500;
    margin-right: 4px;
  }
  &__cell-text4 {
    flex: 0 0 auto;
    color: #f15a89;
    font-size: 13px;
    font-weight: 500;
    margin-right: 4px;
  }
  &__holder {
    position: relative;
    margin-right: -130px;
    @include response(1200px) {
      margin-right: 0;
    }
    &::after {
      content: "";
      display: block;
      clear: both;
    }
  }
  &__share {
    float: right;
    width: 54px;
    @include response(1200px) {
      float: none;
      width: 100%;
      padding-bottom: 20px;
    }
  }
  &__content {
    width: 825px;
    float: left;
    padding-bottom: 50px;
    @include response(1200px) {
      float: none;
      width: 100%;
      padding-bottom: 20px;
    }
  }
  &__content-img {
    max-width: 650px;
    width: 100%;
    margin: 50px auto;
    text-align: center;
  }
  &__content-img-box {
    position: relative;
    margin-bottom: 10px;
  }
  &__content-img-text {
    display: inline-block;
    vertical-align: top;
    padding: 10px 25px;
    font-size: 12px;
    line-height: 160%;
    color: #333;
    background: #f7edff;
    border-radius: 5px;
  }
  &__quote {
    position: relative;
    margin: 50px 0;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    background: #fff;
    border-left: 5px solid $purple;
    padding: 30px 160px 30px 40px;
    @include response(767px) {
      padding: 20px;
    }
  }
  &__quote-decor {
    position: absolute;
    top: 20px;
    right: 20px;
    @include response(767px) {
      display: none;
    }
  }
  &__quote-text {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    margin-bottom: 10px;
    a {
      color: $purple;
      text-decoration: none;
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
  &__quote-link {
    display: inline-flex;
    align-items: center;
    @include desktop-hover() {
      text-decoration: none;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 13px;
    }
  }
  &__quote-link-text {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: $purple;
  }
  &__author {
    position: relative;
    padding-top: 50px;
    padding-bottom: 40px;
    display: flex;
    align-items: center;
    border-top: 1px solid #f7edff;
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__author-ava {
    flex: 0 0 100px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
    margin-right: 20px;
    padding: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 10px;
    }
    img {
      border-radius: 50%;
    }
  }
  &__author-name {
    flex: 0 0 auto;
    margin-right: 40px;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 15px;
      text-align: center;
    }
  }
  &__author-title {
    font-size: 13px;
    line-height: 160%;
    color: #333;
  }
  &__author-value {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: $purple;
    max-width: 200%;
  }
  &__author-text {
    font-size: 14px;
    line-height: 180%;
    color: #333;
    @include response(767px) {
      text-align: center;
    }
  }
  &__bottom {
    display: flex;
    @include response(767px) {
      display: block;
    }
  }
  &__bottom-left {
    flex: 1 1 auto;
    background: #f8f8f8;
    border-radius: 5px;
    padding: 24px 30px;
    margin-right: 10px;
    color: #333;
    font-size: 13px;
    line-height: 160%;
    font-weight: 500;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }
  &__bottom-right {
    display: flex;
    align-items: center;
    padding: 24px 22px;
    color: #fff;
    font-size: 13px;
    line-height: 160%;
    background: #f15a89;
    border-radius: 5px;
    span {
      text-decoration: underline;
    }
    svg {
      flex: 0 0 auto;
      margin-left: 7px;
    }
  }
  &__nav {
    padding: 40px 20px;
    list-style: none;
    margin: 0 0 60px;
    background: #f8f8f8;
    border-radius: 10px;
    li {
      position: relative;
      margin-bottom: 25px;
      position: relative;
      padding-left: 27px;
      line-height: 26px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 8px;
        width: 10px;
        height: 10px;
        background: #6c41bf;
        border-radius: 50%;
      }
      ul {
        list-style: none;
        padding: 0;
        margin: 0 0 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    a {
      display: block;
      text-decoration: none;
      color: #333;
      margin-bottom: 25px;
      @include desktop-hover() {
        color: $purple;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__top-text {
    position: relative;
    padding-top: 50px;
    padding-bottom: 50px;
    border-top: 1px solid #f7edff;
  }
  &__top-text-decor {
    position: absolute;
    bottom: 0;
    left: calc(100% + 57px);
  }
  &__navigation-parent {
    position: relative;
    &--bottom-decor {
      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 550px;
        background: linear-gradient(360deg, rgba(247, 237, 255, 0.5) 0%, rgba(247, 237, 255, 0) 100%);
      }
    }
  }
  &__navigation {
    position: absolute;
    top: 0;
    left: 8vw;
    border: 1px solid #f7edff;
    border-radius: 10px;
    width: 330px;
    background: #fff;
    z-index: 1;
    @include response(1919px) {
      left: 30px;
    }
    @include response(1580px) {
      width: 280px;
      left: 10px;
    }
    @include response(1366px) {
      width: 200px;
    }
    @include response(1200px) {
      display: none;
    }
  }
  &__navigation-title {
    padding: 40px 10px 10px 50px;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333;
    @include response(1420px) {
      padding: 20px 14px 0;
    }
  }
  &__navigation-list {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      border-bottom: 1px solid #f7edff;
      position: relative;
      @include desktop-hover() {
        svg {
          opacity: 1;
        }
      }
      &.svg-top {
        > svg {
          transform: scaleY(-1) rotate(90deg);
        }
      }
      &.active {
        svg {
          opacity: 0;
        }
        > a {
          color: $purple;
        }
      }
      &:last-child {
        border-bottom: 0;
      }
      svg {
        position: absolute;
        left: 20px;
        top: 21px;
        transform: rotate(90deg);
        opacity: 0;
        fill: $purple;
        transition: 0s;
        @include response(1420px) {
          display: none;
        }
      }
    }
    a {
      display: block;
      position: relative;
      text-decoration: none;
      font-size: 14px;
      line-height: 160%;
      padding: 14px 40px 14px 50px;
      color: #333;
      @include response(1420px) {
        padding: 14px;
      }
      @include desktop-hover() {
        color: $purple;
      }
    }
  }
  &__navigation-drop {
    a {
      padding-left: 70px;
      @include response(1420px) {
        padding-left: 30px;
      }
    }
    svg {
      left: 40px !important;
    }
  }
  &__nav-title {
    position: relative;
    margin-bottom: 50px;
    background: #f8f8f8;
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 30px 10px;
    font-weight: bold;
    font-size: 40px;
    line-height: 160%;
    color: #333;
    @include response(1200px) {
      margin-bottom: 30px;
      font-size: 28px;
      padding-top: 15px;
      padding-bottom: 15px;
    }
    @include response(767px) {
      font-size: 24px;
    }
  }
  &__nav-title-counter {
    flex: 0 0 auto;
    margin-right: 30px;
    background: $purple;
    color: #fff;
    font-weight: 500;
    font-size: 15px;
    line-height: 160%;
    padding: 3px 10px;
    border-radius: 3px;
    @include response(1200px) {
      margin-right: 10px;
    }
    @include response(767px) {
      position: absolute;
      top: -15px;
      left: -10px;
    }
  }
  &__tabs {
    position: relative;
    margin-bottom: 50px;
  }
  &__tabs-options {
    position: relative;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    margin-top: 6px;
    padding: 34px 43px 46px;
    @include response(767px) {
      padding: 15px;
    }
  }
  &__tabs-cell {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__tabs-title {
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 20px;
    line-height: 160%;
    color: #333;
  }
  &__tabs-list {
    list-style: none;
    padding: 0 0 0 28px;
    margin-bottom: 0;
    @include response(767px) {
      padding: 0;
    }
    li {
      position: relative;
      padding-left: 24px;
      font-size: 14px;
      line-height: 160%;
      color: #333;
      margin-bottom: 4px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 6px;
        width: 10px;
        height: 10px;
        border: 1px solid #fa9c1f;
        border-radius: 50%;
      }
    }
  }
  &__section {
    position: relative;
    padding-top: 30px;
    margin-bottom: 50px;
    @include response(1200px) {
      padding-left: 25px;
      padding-right: 25px;
      margin-bottom: 40px;
    }
  }
  &__section-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    img {
      width: 100%;
    }
  }
  &__section-img-main {
    flex: 0 0 auto;
    padding-right: 20px;
    @include response(767px) {
      display: none;
    }
  }
  &__section-container {
    max-width: 825px;
    width: 100%;
    margin: 0 auto;
  }
  &__section-holder {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding-bottom: 40px;
    border-bottom: 1px solid #e6e6e6;
    margin-right: -128px;
    @include response(1200px) {
      margin-right: 0;
      padding-bottom: 30px;
    }
  }
  &__section-box {
    position: relative;
    max-width: 580px;
    width: 100%;
  }
  &__section-row {
    position: relative;
    margin-bottom: 35px;
    display: flex;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__section-back {
    flex: 0 0 auto;
    background: #fff;
    border: 1px solid #e9e9e9;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
    display: flex;
    align-items: center;
    padding: 9px 20px;
    color: #333;
    text-decoration: none;
    @include desktop-hover() {
      .blog-page__section-back-link {
        text-decoration: none;
      }
    }
  }
  &__section-back-icon {
    flex: 0 0 auto;
    margin-right: 10px;
  }
  &__section-back-text {
    font-size: 12px;
    line-height: 160%;
    flex: 0 0 auto;
    margin-right: 4px;
  }
  &__section-back-link {
    font-size: 12px;
    line-height: 160%;
    color: #6c41bf;
    font-weight: 500;
    flex: 0 0 auto;
    text-decoration: underline;
  }
  &__section-chapter {
    background: linear-gradient(270deg, #ba58e1 0%, #b457de 7.81%, #6f49bc 100%);
    border-radius: 5px;
    padding: 5px 19px;
    font-weight: bold;
    font-size: 15px;
    line-height: 160%;
    text-align: center;
    color: #fff;
    display: inline-block;
    vertical-align: top;
    margin-bottom: 10px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__section-title {
    margin: 0 0 10px;
    font-weight: bold;
    font-size: 40px;
    line-height: 160%;
    color: #333;
    @include response(1200px) {
      font-size: 20px;
      margin-bottom: 20px;
    }
  }
  &__section-info {
    position: relative;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    @include response(767px) {
      margin-bottom: 0;
    }
  }
  &__section-cell {
    flex: 0 0 auto;
    margin-bottom: 10px;
    margin-right: 40px;
    display: flex;
    align-items: center;
    @include response(767px) {
      display: block;
      margin-right: 20px;
    }
  }
  &__section-cell-title {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    font-size: 13px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
    margin-right: 4px;
    svg {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__section-cell-val {
    font-size: 13px;
    line-height: 160%;
    color: #6c41bf;
    font-weight: 500;
    @include response(767px) {
      padding-left: 26px;
    }
  }
  &__section-next {
    flex: 0 0 auto;
    display: inline-block;
    vertical-align: top;
    background: #fff;
    border: 1px solid #ededed;
    padding: 12px 12px 12px 30px;
    min-width: 280px;
    text-decoration: none;
    border-radius: 10px;
    @include response(767px) {
      display: none;
    }
    @include desktop-hover() {
      .blog-page__section-next-text {
        text-decoration: none;
      }
    }
  }
  &__section-prev {
    flex: 0 0 auto;
    display: inline-block;
    vertical-align: top;
    background: #fff;
    border: 1px solid #ededed;
    padding: 12px 30px 12px 12px;
    min-width: 280px;
    text-decoration: none;
    border-radius: 10px 0 0 10px;
    & + .blog-page__section-next {
      border-radius: 0 10px 10px 0;
    }
    @include response(767px) {
      display: none;
    }
    @include desktop-hover() {
      .blog-page__section-next-text {
        text-decoration: none;
      }
    }
  }
  &__section-btns {
    display: flex;
  }
  &__section-next-title {
    font-size: 10px;
    line-height: 160%;
    text-transform: uppercase;
    color: #757575;
    display: block;
    .blog-page__section-prev & {
      text-align: right;
    }
  }
  &__section-next-row {
    display: flex;
    align-items: center;
    svg {
      flex: 0 0 auto;
    }
    .blog-page__section-prev & {
      justify-content: flex-end;
      svg {
        transform: scaleX(-1);
      }
    }
  }
  &__section-next-text {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    text-decoration-line: underline;
    color: #333;
    flex: 0 0 auto;
    margin-right: 15px;
    .blog-page__section-prev & {
