.step {
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  &__number {
    font-weight: bold;
    font-size: 64px;
    line-height: 1;
    flex: 0 0 auto;
    margin-right: 25px;
    color: #f4bbb3;
  }
  &__title {
    font-weight: bold;
    font-size: 24px;
    line-height: 160%;
    color: #333;
  }
}
