.article2 {
  position: relative;
  padding-top: 40px;
  padding-bottom: 30px;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  &__title {
    margin-bottom: 30px;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #333;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
  }
  &__link {
    margin-bottom: 10px;
    margin-right: 65px;
    text-decoration: none;
    font-size: 16px;
    line-height: 160%;
    color: #333;
    border-bottom: 4px solid #f7edff;
    @include desktop-hover() {
      border-bottom-color: #6c41bf;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}
