.landing {
  position: relative;
  &__top {
    position: relative;
    background: linear-gradient(85.32deg, #f1dfff 14.2%, #fff1e7 90.73%);
    @include response(1200px) {
      background: linear-gradient(166.4deg, #f1dfff 21.69%, #fff1e7 80.4%);
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      padding-top: 105px;
      padding-bottom: 90px;
      .container {
        position: initial;
      }
    }
  }
  &__top-box {
    display: flex;
    align-items: center;
  }
  &__top-left {
    flex: 1 1 auto;
    padding-right: 74px;
    @include response(1200px) {
      padding-right: 0;
    }
  }
  &__top-title {
    margin: 0 0 30px;
    font-weight: bold;
    font-size: 40px;
    line-height: 140%;
    color: #333;
    @include response(1200px) {
      font-size: 28px;
      line-height: 160%;
      margin-bottom: 25px;
    }
    span {
      color: $purple;
    }
  }
  &__top-text {
    margin-bottom: 50px;
    font-size: 20px;
    line-height: 160%;
    color: #333;
    @include response(1200px) {
      margin-bottom: 40px;
    }
    p {
      margin: 0;
    }
  }
  &__top-buy-box {
    position: relative;
    margin-bottom: 30px;
  }
  &__top-buy-btn {
    position: relative;
  }
  &__top-down-box {
    position: relative;
  }
  &__top-down-btn {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333;
    @include desktop-hover() {
      color: $purple;
      span {
        transform: translateY(2px);
      }
    }
    span {
      flex: 0 0 28px;
      width: 28px;
      max-width: 28px;
      margin-left: 15px;
      height: 28px;
      background: #fff;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.3s ease-in-out;
    }
  }
  &__top-right {
    flex: 0 0 auto;
    position: relative;
    @include response(1200px) {
      position: initial;
    }
  }
  &__decor1 {
    position: absolute;
    z-index: 1;
    bottom: 34px;
    left: 0;
    transform: translateX(-60%);
    @include response(1200px) {
      width: 200px;
      left: auto;
      right: -30px;
      bottom: 10px;
      transform: none;
      max-width: none;
    }
  }
  &__decor2 {
    position: absolute;
    z-index: 1;
    top: 58px;
    right: 0;
    transform: translateX(60%);
    @include response(1200px) {
      display: none;
    }
  }
  &__top-img {
    position: relative;
    @include response(1200px) {
      display: none;
    }
  }
  &__nav-mobile {
    display: none;
    @include response(1200px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 15px;
      background: #f7edff;
      margin-left: -25px;
      margin-right: -25px;
    }
  }
  &__nav-mobile-number {
    @include response(1200px) {
      flex: 0 0 auto;
      font-weight: bold;
      font-size: 12px;
      line-height: 11px;
      color: #6c41bf;
    }
  }
  &__nav-mobile-title {
    @include response(1200px) {
      font-size: 14px;
      line-height: 150%;
      text-align: center;
      color: #000;
      flex: 1 1 auto;
      padding-left: 10px;
      padding-right: 20px;
      font-weight: 500;
    }
  }
  &__nav-mobile-dots {
    flex: 0 0 30px;
    width: 30px;
    max-width: 30px;
    height: 30px;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
  }
  &__nav {
    position: relative;
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: 50px;
    @include response(1200px) {
      position: fixed;
      top: 105px;
      left: 0;
      right: 0;
      z-index: 10;
      &--scroll {
        transform: translateY(-105px);
      }
    }
  }
  &__nav-list {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0;
    margin: 0;
    @include response(1200px) {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      display: block;
      background: #fff;
      opacity: 0;
      visibility: hidden;
      transform: translateY(10px);
      transition: all 0.3s ease-in-out;
      .landing__nav.active & {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
      }
    }
  }
  &__nav-item {
    width: 100%;
    position: relative;
  }
  &__nav-btn {
    display: block;
    text-decoration: none;
    height: 100%;
    padding: 18px 14px;
    text-align: center;
    border: 1px solid #e6e6e6;
    @include response(1200px) {
      text-align: right;
      border-left: 0;
      border-right: 0;
      border-top: 0;
    }
    @include desktop-hover() {
      border-color: $purple;
      background: #f7edff;
      .landing__nav-icon {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
      }
    }
  }
  &__nav-number {
    display: block;
    margin-bottom: 9px;
    font-weight: bold;
    font-size: 12px;
    line-height: 11px;
    color: #6c41bf;
    @include response(1200px) {
      display: none;
    }
  }
  &__nav-name {
    display: block;
    font-size: 16px;
    line-height: 150%;
    color: #000;
    font-weight: 500;
  }
  &__nav-icon {
    position: absolute;
    background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
    width: 28px;
    height: 28px;
    bottom: -14px;
    left: 50%;
    margin-left: -14px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-5px);
    transition: all 0.3s ease-in-out;
    @include response(1200px) {
      display: none;
    }
  }
  &__what-are {
    position: relative;
    padding-top: 50px;
    padding-bottom: 100px;
    @include response(1200px) {
      padding-top: 75px;
      padding-bottom: 50px;
    }
  }
  &__what-are-title {
    margin: 0 0 50px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__what-are-text {
    font-size: 18px;
    line-height: 160%;
    text-align: center;
    color: #333;
    max-width: 821px;
    width: 100%;
    margin: 0 auto 56px;
    @include response(1200px) {
      margin-bottom: 50px;
    }
  }
  &__what-are-list {
    display: flex;
    justify-content: center;
    @include response(666px) {
      display: block;
    }
  }
  &__what-are-item {
    max-width: 203px;
    width: 100%;
    position: relative;
    text-align: center;
    margin-left: 50px;
    margin-right: 50px;
    @include response(1200px) {
      margin-left: 20px;
      margin-right: 20px;
    }
    @include response(666px) {
      display: flex;
      align-items: center;
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 50px;
      max-width: 100%;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__what-are-img {
    margin-bottom: 29px;
    @include response(666px) {
      margin-bottom: 0;
      flex: 0 0 150px;
      width: 150px;
      text-align: center;
      padding-left: 10px;
      padding-right: 20px;
    }
  }
  &__what-are-descriptions {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
    @include response(666px) {
      text-align: left;
      flex: 1 1 auto;
    }
    p {
      margin: 0;
    }
  }
  &__benefits {
    position: relative;
    padding-top: 50px;
    padding-bottom: 75px;
    margin-bottom: 50px;
    @include response(1200px) {
      padding-top: 35px;
      margin-bottom: 35px;
    }
  }
  &__benefits-title {
    margin-bottom: 40px;
  }
  &__benefits-text {
    margin-bottom: 85px;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: #6c41bf;
    @include response(1200px) {
      margin-bottom: 194px;
    }
  }
  &__benefits-box {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      bottom: -75px;
      top: 60px;
      width: 100vw;
      background: linear-gradient(85.32deg, #f1dfff 14.2%, #fff1e7 90.73%);
      left: 50%;
      transform: translateX(-50%);
      @include response(1200px) {
        top: 170px;
      }
    }
  }
  &__benefits-triggers {
    position: relative;
    background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
    border-radius: 5px;
    margin-top: -60px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 34px;
    margin-bottom: 50px;
    @include response(1200px) {
      flex-wrap: wrap;
      padding-bottom: 4px;
      margin-top: -170px;
    }
  }
  &__benefits-triggers-item {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 160%;
    color: #fff;
    margin-left: 10px;
    margin-right: 10px;
    @include response(1200px) {
      flex: 0 0 calc(50% - 20px);
      max-width: calc(50% - 20px);
      margin-bottom: 30px;
    }
    @include response(767px) {
      flex: 0 0 calc(100% - 20px);
      max-width: calc(100% - 20px);
    }
    img {
      flex: 0 0 auto;
      margin-right: 20px;
    }
  }
  &__benefits-text2 {
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: #6c41bf;
    margin-bottom: 50px;
    position: relative;
  }
  &__benefits-list {
    position: relative;
    display: flex;
    justify-content: center;
    @include response(767px) {
      display: block;
    }
  }
  &__benefits-item {
    margin-left: 50px;
    margin-right: 50px;
    flex: 0 0 250px;
    max-width: 250px;
    width: 100%;
    @include response(1200px) {
      margin-left: 15px;
      margin-right: 15px;
      flex: 0 0 230px;
      max-width: 230px;
    }
    @include response(767px) {
      max-width: 100%;
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 39px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &:nth-child(1) {
      margin-top: -50px;
      @include response(767px) {
        margin-top: 0;
      }
      .landing__benefits-img {
        text-align: right;
        margin-right: -40px;
        @include response(1200px) {
          margin-right: 0;
          text-align: center;
        }
      }
    }
    &:nth-child(3) {
      margin-top: -50px;
      @include response(767px) {
        margin-top: 0;
      }
      .landing__benefits-img {
        text-align: left;
        margin-left: -40px;
        @include response(1200px) {
          margin-left: 0;
          text-align: center;
        }
      }
    }
  }
  &__benefits-img {
    margin-bottom: 35px;
    text-align: center;
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__benefits-item-title {
    margin-bottom: 15px;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: #333;
  }
  &__benefits-item-text {
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: #333;
    padding: 10px 15px;
    background: #fff;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    margin-bottom: 20px;
    font-weight: 500;
  }
  &__benefits-item-btn-box {
    position: relative;
  }
  &__benefits-item-btn {
    display: flex;
    align-items: center;
    text-decoration: none;
    justify-content: center;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #6c41bf;
    @include desktop-hover() {
      text-decoration: underline;
    }
    svg {
      flex: 0 0 auto;
      margin-left: 10px;
    }
  }
  &__team {
    position: relative;
    padding-top: 50px;
    padding-bottom: 100px;
    max-width: 825px;
    width: 100%;
    margin: 0 auto;
    @include response(1200px) {
      padding-top: 35px;
      padding-bottom: 152px;
    }
  }
  &__team-decor {
    position: absolute;
    bottom: 0;
    right: 0;
    @include response(1200px) {
      right: auto;
      left: 50%;
      transform: translateX(-50%);
      max-width: 96vw;
    }
    @include response(767px) {
      max-width: 80vw;
    }
  }
  &__team-head {
    display: flex;
    align-items: center;
    margin-bottom: 75px;
    @include response(767px) {
      display: block;
      margin-bottom: 30px;
    }
  }
  &__team-title {
    max-width: 246px;
    flex: 0 0 246px;
    width: 100%;
    margin-right: 75px;
    @include response(767px) {
      margin-right: 0;
      max-width: 100%;
    }
    &.title {
      text-align: left;
    }
  }
  &__team-text {
    position: relative;
    flex: 1 1 auto;
    @include response(767px) {
      margin-bottom: 35px;
    }
  }
  &__team-triggers {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 112px;
    margin-left: -50px;
    @include response(1200px) {
      margin-left: -20px;
    }
    @include response(767px) {
      display: block;
      margin-bottom: 35px;
      margin-left: 0;
    }
  }
  &__team-triggers-item {
    flex: 0 0 auto;
    padding-left: 40px;
    border-left: 1px solid #e9e9e9;
    display: flex;
    align-items: center;
    margin-left: 50px;
    @include response(1200px) {
      margin-left: 20px;
      padding-left: 10px;
    }
    @include response(767px) {
      margin-left: 0;
      padding-left: 0;
      border-left: 0;
      margin-bottom: 35px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &:first-child {
      padding-left: 0;
      border-left: 0;
    }
  }
  &__team-triggers-img {
    flex: 0 0 auto;
    margin-right: 20px;
  }
  &__team-triggers-text {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    span {
      font-weight: 500;
      display: block;
    }
  }
  &__team-list {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    @include response(767px) {
      display: block;
    }
  }
  &__team-item {
    position: relative;
    margin-left: 25px;
    margin-right: 25px;
    flex: 0 0 350px;
    max-width: 350px;
    width: 100%;
    text-align: center;
    padding: 0 50px 40px;
    @include response(767px) {
      max-width: 400px;
      margin: 0 auto 40px;
      padding: 0 30px 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &:nth-child(2) {
      margin-top: -50px;
      @include response(767px) {
        margin-top: 0;
      }
    }
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      top: 88px;
      background: linear-gradient(180deg, #f7edff 0%, #fdf8ff 100%);
      border-radius: 5px;
    }
  }
  &__team-ava {
    position: relative;
    width: 175px;
    height: 175px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto 20px;
    @include response(767px) {
      width: 150px;
      height: 150px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__team-name {
    position: relative;
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #333;
  }
  &__team-position {
    position: relative;
    display: inline-flex;
    align-items: center;
    border-radius: 5px;
    color: #fff;
    font-weight: bold;
    font-size: 15px;
    line-height: 160%;
    padding: 5px 20px;
    margin-bottom: 40px;
    svg {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__team-item-text {
    text-align: left;
  }
  &__info {
    position: relative;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &__info-title {
    text-align: center;
    margin-bottom: 25px;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #333;
  }
  &__info-btn-box {
    text-align: center;
  }
}
