.info {
  position: relative;
  background: #f7edff;
  border-radius: 10px;
  padding: 50px 43px 50px 50px;
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  @include response(1200px) {
    padding: 60px 30px 40px;
  }
  @include response(767px) {
    display: block;
  }
  &--red {
    background: #fef5f6;
    padding: 63px 74px;
    @include response(1200px) {
      padding: 30px 30px 40px;
    }
  }
  &__decor {
    position: absolute;
    left: 50px;
    top: -27px;
    img {
      box-shadow: none !important;
    }
  }
  &__text {
    position: relative;
    flex: 1 1 auto;
  }
  &__title {
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #333;
    margin-bottom: 25px;
  }
  &__descriptions {
    font-size: 18px;
    line-height: 175%;
    margin-bottom: 25px;
    &:last-child {
      margin-bottom: 0;
    }
    &--fz16 {
      font-size: 16px;
      line-height: 28px;
    }
  }
  &__link {
    display: inline-flex;
    align-items: center;
    margin-bottom: 25px;
    font-size: 16px;
    line-height: 160%;
    color: #6c41bf;
    text-decoration: none;
    @include response(767px) {
      flex-wrap: wrap;
    }
    @include desktop-hover() {
      text-decoration: underline !important;
    }
    svg {
      flex: 0 0 auto;
      margin-left: 10px;
    }
  }
  &__bottom {
    position: relative;
    svg {
      margin-left: 4px;
      display: inline-block;
      vertical-align: middle;
    }
  }
  &__img {
    flex: 0 0 auto;
    margin-left: 44px;
    @include response(767px) {
      display: none;
      .info--red & {
        display: block;
        margin-left: 0;
        margin-top: 30px;
        text-align: center;
      }
    }
    img {
      box-shadow: none !important;
    }
  }
}
