.conclusion-box {
  position: relative;
  border-top: 1px solid #e6e6e6;
  padding-top: 40px;
  padding-bottom: 40px;
  &__head {
    display: flex;
    align-items: center;
    margin-bottom: 22px;
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #333;
    .text-content & {
      img {
        flex: 0 0 auto;
        margin-right: 21px;
      }
    }
  }
}
