.benefits-content {
  position: relative;
  background: #fff;
  border: 1px solid #e9e9e9;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 50px;
  margin-bottom: 25px;
  @include response(1200px) {
    padding: 20px;
  }
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    @include response(767px) {
      display: block;
    }
  }
  &__title {
    font-weight: bold;
    font-size: 26px;
    line-height: 160%;
    color: #333;
    @include response(767px) {
      font-size: 20px;
    }
  }
  &__icon {
    flex: 0 0 auto;
    margin-right: 30px;
    @include response(767px) {
      margin-bottom: 10px;
    }
    .text-content & {
      img {
        box-shadow: none;
      }
    }
  }
  &__body {
    position: relative;
    font-size: 16px;
    line-height: 160%;
    color: #333;
  }
}
