.product-info {
  position: relative;
  padding-top: 50px;
  @include response(1200px) {
    padding-top: 30px;
    margin-bottom: 40px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -25px;
      right: -25px;
      height: 1px;
      background: #e9e9e9;
    }
  }
  &__item {
    position: relative;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-head {
    border: 1px solid #e9e9e9;
    border-radius: 10px;
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 50px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    color: #333;
    @include response(1200px) {
      padding-left: 25px;
      padding-right: 25px;
    }
    @include desktop-hover() {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      border-color: transparent;
    }
    .product-info__item.active & {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      border-color: transparent;
      svg {
        transform: scaleY(-1);
      }
    }
    svg {
      transition: all 0.3s ease-in-out;
    }
  }
  &__item-body {
    padding: 40px;
    display: none;
    @include response(1200px) {
      padding: 30px 0;
    }
    .product-info__item.active & {
      display: block;
    }
  }
  &__show-more {
    display: none;
    @include response(1200px) {
      display: inline-block;
      margin-top: 20px;
      text-transform: uppercase;
      color: #6c41bf;
      font-weight: 700;
      text-decoration: underline;
    }
  }
  &__show-open {
    .product-info__item-body.active & {
      display: none;
    }
  }
  &__show-close {
    display: none;
    .product-info__item-body.active & {
      display: block;
    }
  }
  &__item-text {
    @include response(1200px) {
      max-height: 200px;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 8;
      -webkit-box-orient: vertical;
      .product-info__item-body.active & {
        max-height: none;
      -webkit-line-clamp: initial;

      }
    }
  }
}
